import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';

import {view} from './view.js';

createCustomElement('sn-aa-article-header',{
	renderer : {
		type: snabbdom,
		view
	},
	properties : {
		parentKey : {default : ''},
		formRecordInfo: {default : ''},
		actionStates : {default : ''},
		data : {default : ''},
		source : {default : ''},
		actions : {default : ''},
		isNewRecord : {default : ''},
		itemIndex : {default : ''},
		isRecommendation : {default : ''},
		formTimeStamp : {default : Date.now()}
	},
	styles
});