import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	AA_LOAD_CATALOG_DETAIL_REQUEST,
	AA_LOAD_CATALOG_DETAIL_STARTED,
	AA_LOAD_CATALOG_DETAIL_SUCCEEDED,
	AA_LOAD_CATALOG_DETAIL_FAILED
} from './actions';

const catalogQuery = `query ($sys_id: String!) {
	GlideRecord_Query {
		  sc_cat_item(sys_id: $sys_id) {
			  _results {
				  sys_id {
					  value
				  }
				  name {
					  value
					  displayValue
				  }
				  short_description {
					  displayValue
				  }
				  description {
					  displayValue
				  }
				  price {
					  value
					  displayValue
				  }
				  ignore_price {
					  value
					  displayValue
				  }
				  picture {
					  displayValue
				  }
			  }
		  }
	  }
  }`;

const fetchCatalogDetailEffect = createGraphQLEffect(catalogQuery, {
	variableList: ['sys_id'],
	startActionType: AA_LOAD_CATALOG_DETAIL_STARTED,
	successActionType: AA_LOAD_CATALOG_DETAIL_SUCCEEDED,
	errorActionType: AA_LOAD_CATALOG_DETAIL_FAILED
});

const fetchCatalogDetailStartedEffect = coeffects => {
	const { state, updateState } = coeffects;
	updateState({
		...state,
		isLoading: true
	});
};

const fetchCatalogDetailSucceededEffect = coeffects => {
	const { state, action, updateState } = coeffects;
	const results = get(
		action,
		'payload.data.GlideRecord_Query.sc_cat_item._results',
		[]
	);
	const item = isEmpty(results) ? null : results[0];

	updateState({
		...state,
		catalogItem: item,
		isLoading: false,
		currentSysId: item.sys_id.value
	});
};

export default {
	[AA_LOAD_CATALOG_DETAIL_REQUEST]: {
		...fetchCatalogDetailEffect,
		stopPropagation: true
	},
	[AA_LOAD_CATALOG_DETAIL_STARTED]: {
		effect: fetchCatalogDetailStartedEffect,
		stopPropagation: true
	},
	[AA_LOAD_CATALOG_DETAIL_SUCCEEDED]: {
		effect: fetchCatalogDetailSucceededEffect,
		stopPropagation: true
	}
	// [AA_LOAD_CATALOG_DETAIL_FAILED]: {
	// 	...logErrorEffect,
	// 	stopPropagation: true
	// }
};
