import get from 'lodash/get';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import makeUIActionRestEffect from './makeUIActionRestEffect';
import { setFormFields, showTabMessage } from '../../util/agentAssistUtils';
import '@servicenow/now-alert';
import { RESET_MODAL_STATE } from './uiActionCommonUtils';
import { isEmptyObj } from '../../util/agentAssistUtils';
import { AA_UPDATE_REL_DOC } from '../actions';

export const REST_ACTION_ID = 'REST_ACTION';
const AA_REST_ACTION_MODAL_SUBMITTED =
	'AGENT_ASSIST#REST_ACTION_MODAL_SUBMITTED';
const AA_REST_ACTION_MODAL_CANCELED = 'AGENT_ASSIST#REST_ACTION_MODAL_CANCELED';
const AA_REST_ACTION_PREMODAL_STARTED =
	'AGENT_ASSIST#REST_ACTION_PREMODAL_STARTED';
const AA_REST_ACTION_PREMODAL_SUCCEEDED =
	'AGENT_ASSIST#REST_ACTION_PREMODAL_SUCCEEDED';
const AA_REST_ACTION_PREMODAL_FAILED =
	'AGENT_ASSIST#REST_ACTION_PREMODAL_FAILED';
const AA_REST_ACTION_POSTMODAL_STARTED =
	'AGENT_ASSIST#AA_REST_ACTION_POSTMODAL_STARTED';
const AA_REST_ACTION_POSTMODAL_SUCCEEDED =
	'AGENT_ASSIST#REST_ACTION_POSTMODAL_SUCCEEDED';
const AA_REST_ACTION_POSTMODAL_FAILED =
	'AGENT_ASSIST#REST_ACTION_POSTMODAL_FAILED';
const AA_REST_MODAL_INPUT = 'AGENT_ASSIST#REST_MODAL_INPUT';
const AA_REST_ACTION_MODAL_COMPLETED =
	'AGENT_ASSIST#REST_ACTION_MODAL_COMPLETED';
const DEFAULT_ERROR_MESSAGE = t(
	'Something went wrong. This action is not available.'
);

const restUtils = {
	getInnerComponent(
		restModalState = {},
		currentFields = {},
		userInputObj = {},
		dispatch
	) {
		//eslint-disable-line no-unused-vars
		const _inputHandler = userInputObj => {
			dispatch(AA_REST_MODAL_INPUT, userInputObj);
		};
		const { modalInfoMsg, modalFields } = restModalState;
		const alert = modalInfoMsg ? (
			<now-alert
				expanded="false"
				manageExpanded="false"
				status="info"
				icon-name="circle-info-outline"
				content={modalInfoMsg}
			></now-alert>
		) : null;

		const modalBody = modalFields
			? modalFields.map(item => {
					return !get(currentFields, item.name, null) ? null : (
						<div className="aa-modal--input" key={item.name}>
							<sn-record-input
								type="textarea"
								label={item.label}
								value={get(
									userInputObj,
									`${item.name}.value`,
									`${item.value || ''}`
								)}
								maxlength="4000"
								onValueChange={({ value }) =>
									_inputHandler({
										[item.name]: {
											value,
											displayValue: value
										}
									})
								}
							/>
						</div>
					);
			  })
			: null;

		return (
			// eslint-disable-line no-unused-vars
			<Fragment>
				<div className="aa-modal--body">
					{alert}
					{modalBody}
				</div>
			</Fragment>
		);
	},

	getFooterActions(userInputObj = '', modalState = {}) {
		return [
			{
				variant: 'primary',
				label: modalState.actionDisplayValue,
				clickActionType: AA_REST_ACTION_MODAL_SUBMITTED,
				userInputObj
			},
			{
				variant: 'secondary',
				label: t('Cancel'),
				clickActionType: RESET_MODAL_STATE
			}
		];
	},

	getHeaderLabel(restModalState = {}) {
		return restModalState.modalTitle;
	},

	getInitialModalState() {
		return {
			[REST_ACTION_ID]: {}
		};
	},
	actionHandlers: {
		modalInternalActions: {
			[AA_REST_MODAL_INPUT]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					updateState({
						...state,
						userInputObj: {
							...state.userInputObj,
							...action.payload
						}
					});
				},
				stopPropagation: true
			},

			[AA_REST_ACTION_MODAL_SUBMITTED]: coeffects => {
				const {
					action: {
						payload: { footerAction: { userInputObj } } = {
							footerAction: { userInputObj: {} }
						}
					},
					state: {
						properties: {
							currentTable,
							currentId,
							modalState: {
								[REST_ACTION_ID]: {
									uiActionConfig,
									itemData,
									postModalRestRequired
								}
							} = {
								[REST_ACTION_ID]: {
									uiActionConfig: {},
									itemData: {},
									postModalRestRequired: false
								}
							}
						}
					},
					dispatch
				} = coeffects;

				const [table, recordId] = itemData.id.split(':');
				dispatch(AA_REST_ACTION_POSTMODAL_STARTED, {
					userInputObj,
					postModalRestRequired,
					requestData: {
						resourceTable: table,
						resourceId: recordId,
						currentTable,
						currentId,
						actionId: uiActionConfig.actionId,
						actionStage: 'post_modal'
					},
					actionPath: uiActionConfig.restAPI
				});
			},

			[AA_REST_ACTION_POSTMODAL_STARTED]: {
				effect: coeffects => {
					const { state, updateState } = coeffects;
					updateState({
						...state,
						properties: {
							...state.properties,
							modalState: {
								...state.properties.modalState,
								[REST_ACTION_ID]: {
									...state.properties.modalState[
										REST_ACTION_ID
									],
									actionDisplayValue: t('Please wait...')
								}
							}
						}
					});
				},

				interceptors: [
					{
						before: context => {
							const {
								coeffects: {
									action: {
										payload: {
											postModalRestRequired,
											userInputObj
										}
									} = {
										payload: {
											postModalRestRequired: false,
											userInputObj: {}
										}
									},
									state: {
										properties: {
											modalState: {
												[REST_ACTION_ID]: { infoMsg }
											}
										} = {
											modalState: {
												[REST_ACTION_ID]: {
													infoMsg: ''
												}
											}
										}
									},
									dispatch
								}
							} = context;
							let newEffect = {};
							if (postModalRestRequired) {
								newEffect = {
									...makeUIActionRestEffect(
										AA_REST_ACTION_POSTMODAL_SUCCEEDED,
										AA_REST_ACTION_POSTMODAL_FAILED
									),
									args: [
										'/api/now/cxs_actions/:actionPath',
										{
											method: 'POST',
											dataParam: 'requestData',
											pathParams: ['actionPath'],
											successActionType: AA_REST_ACTION_POSTMODAL_SUCCEEDED,
											errorActionType: AA_REST_ACTION_POSTMODAL_FAILED
										},
										context.coeffects
									]
								};
							} else {
								newEffect = () => {
									dispatch(AA_REST_ACTION_MODAL_COMPLETED, {
										successful: true,
										infoMsg,
										userInputObj
									});
								};
							}
							return {
								...context,
								effects: [...context.effects, newEffect]
							};
						}
					}
				],
				stopPropagation: true
			},

			[AA_REST_ACTION_POSTMODAL_SUCCEEDED]: {
				effect: () => {},
				args: [],
				interceptors: [
					{
						before: context => {
							const {
								coeffects: {
									action: {
										payload: {
											result: {
												successful,
												infoMsg,
												errMsg,
												updateCurrentRecord,
												updateCurrentFields
											}
										} = {
											result: {
												successful: false,
												infoMsg: '',
												errMsg: '',
												updateCurrentRecord: false,
												updateCurrentFields: {}
											}
										}
									},
									state: {
										userInputObj,
										properties: {
											modalState: {
												[REST_ACTION_ID]: {
													uiActionConfig
												}
											}
										}
									} = {
										userInputObj: {},
										properties: {
											modalState: {
												[REST_ACTION_ID]: {}
											}
										}
									},
									dispatch
								}
							} = context;
							let newEffect = {};
							if (successful) {
								newEffect = () => {
									dispatch(AA_REST_ACTION_MODAL_COMPLETED, {
										successful,
										updateCurrentRecord,
										updateCurrentFields,
										userInputObj,
										infoMsg,
										errMsg
									});
								};
							} else {
								newEffect = {
									effect: coeffects => {
										const {
											state,
											updateState
										} = coeffects;
										updateState({
											...state,
											properties: {
												...state.properties,
												modalState: {
													...state.properties
														.modalState,
													[REST_ACTION_ID]: {
														...state.properties
															.modalState[
															REST_ACTION_ID
														],
														modalInfoMsg:
															errMsg ||
															DEFAULT_ERROR_MESSAGE,
														actionDisplayValue:
															uiActionConfig.displayValue
													}
												}
											}
										});
									},
									args: [context.coeffects]
								};
							}
							return {
								...context,
								effects: [...context.effects, newEffect]
							};
						}
					}
				],
				stopPropagation: true
			}
		},

		modalExternalActions: {
			[AA_REST_ACTION_PREMODAL_STARTED]: {
				...makeUIActionRestEffect(
					AA_REST_ACTION_PREMODAL_SUCCEEDED,
					AA_REST_ACTION_PREMODAL_FAILED
				),
				interceptors: [
					{
						before: context => ({
							...context,
							effects: [
								...context.effects,
								{
									effect: coeffects => {
										const {
											state,
											action,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												[REST_ACTION_ID]: {
													...state.modalState[
														REST_ACTION_ID
													],
													...action.payload.result,
													itemData:
														action.payload.itemData,
													itemIndex:
														action.payload
															.itemIndex,
													uiActionConfig:
														action.payload
															.uiActionConfig,
													actionDisplayValue:
														action.payload
															.actionDisplayValue
												}
											}
										});
									},
									args: [context.coeffects]
								}
							]
						})
					}
				],
				stopPropagation: true
			},

			[AA_REST_ACTION_PREMODAL_SUCCEEDED]: {
				effect: () => {},
				args: [],
				interceptors: [
					{
						before: context => {
							const {
								coeffects: {
									action: {
										payload: {
											result: {
												successful,
												useModal,
												infoMsg,
												errMsg,
												updateCurrentRecord,
												updateCurrentFields
											}
										} = {
											result: {
												successful: false,
												useModal: false,
												infoMsg: '',
												updateCurrentRecord: false,
												updateCurrentFields: {}
											}
										},
										payload
									},
									dispatch,
									state: {
										properties: {
											table: currentTable,
											sysId: currentSysId
										},
										modalState: {
											[REST_ACTION_ID]: {
												itemData,
												itemIndex,
												uiActionConfig
											}
										}
									} = {
										properties: {
											table: '',
											sysId: ''
										},
										modalState: {
											[REST_ACTION_ID]: {
												itemData: {},
												itemIndex: {},
												uiActionConfig: {}
											}
										}
									}
								},
								coeffects
							} = context;
							let newEffect = {};
							if (!successful) {
								newEffect = () => {
									showTabMessage(
										'error',
										errMsg || DEFAULT_ERROR_MESSAGE,
										currentTable,
										currentSysId,
										dispatch
									);
									dispatch(RESET_MODAL_STATE);
								};
							} else {
								if (useModal) {
									newEffect = {
										effect: coeffects => {
											const {
												state,
												updateState
											} = coeffects;
											updateState({
												...state,
												modalState: {
													...state.modalState,
													active: true,
													activeModal: REST_ACTION_ID,
													[REST_ACTION_ID]: {
														...state.modalState[
															REST_ACTION_ID
														],
														...payload.result
													}
												}
											});
										},
										args: [coeffects]
									};
								} else {
									newEffect = () => {
										dispatch(AA_UPDATE_REL_DOC, {
											itemData,
											itemIndex,
											actionValue: uiActionConfig.value,
											remove: false
										});
										if (
											updateCurrentRecord &&
											!isEmptyObj(updateCurrentFields)
										) {
											setFormFields(
												{ ...updateCurrentFields },
												true, // save form
												dispatch
											);
										}
										dispatch(RESET_MODAL_STATE);
										if (infoMsg) {
											showTabMessage(
												'info',
												infoMsg,
												currentTable,
												currentSysId,
												dispatch
											);
										}
									};
								}
							}

							return {
								...context,
								effects: [...context.effects, newEffect]
							};
						}
					}
				],
				stopPropagation: true
			},

			[AA_REST_ACTION_PREMODAL_FAILED]: { stopPropagation: true },

			[AA_REST_ACTION_MODAL_CANCELED]: ({ dispatch }) => {
				dispatch(RESET_MODAL_STATE);
			},

			[AA_REST_ACTION_POSTMODAL_FAILED]: { stopPropagation: true },

			[AA_REST_ACTION_MODAL_COMPLETED]: coeffects => {
				const {
					state: {
						modalState: {
							[REST_ACTION_ID]: {
								itemData,
								itemIndex,
								uiActionConfig,
								updateCurrentRecord: updateCurrentRecordPreModal,
								updateCurrentFields: updateCurrentFieldsPreModal
							}
						},
						properties: { sysId: currentSysId, table: currentTable }
					} = {
						modalState: {
							[REST_ACTION_ID]: {
								itemData: {},
								itemIndex: {},
								uiActionConfig: {},
								updateCurrentRecordPreModal: false,
								updateCurrentFieldsPreModal: {}
							}
						},
						properties: { sysId: '', table: '' }
					},
					action: {
						payload: {
							successful,
							updateCurrentRecord: updateCurrentRecordPostModal,
							updateCurrentFields: updateCurrentFieldsPostModal,
							userInputObj,
							infoMsg,
							errMsg
						}
					} = {
						payload: {
							successful: false,
							updateCurrentRecordPostModal: false,
							updateCurrentFieldsPostModal: {},
							userInputObj: {},
							infoMsg: '',
							errMsg: ''
						}
					},
					dispatch
				} = coeffects;

				if (successful) {
					if (
						updateCurrentRecordPreModal ||
						updateCurrentRecordPostModal ||
						!isEmptyObj(userInputObj)
					) {
						setFormFields(
							{
								...updateCurrentFieldsPreModal,
								...updateCurrentFieldsPostModal,
								...userInputObj
							},
							true, // save form
							dispatch
						);
					}
					dispatch(AA_UPDATE_REL_DOC, {
						itemData,
						itemIndex,
						actionValue: uiActionConfig.value,
						remove: false
					});
					if (infoMsg) {
						showTabMessage(
							'info',
							infoMsg,
							currentTable,
							currentSysId,
							dispatch
						);
					}
					dispatch(RESET_MODAL_STATE);
				} else {
					showTabMessage(
						'info',
						errMsg || DEFAULT_ERROR_MESSAGE,
						currentTable,
						currentSysId,
						dispatch
					);
				}
			}
		}
	},

	startRestAction: (
		{ action, data, itemIndex, currentId, currentTable },
		dispatch
	) => {
		const [table, recordId] = data.id.split(':');
		if (!table || !recordId) {
			return;
		}

		dispatch(AA_REST_ACTION_PREMODAL_STARTED, {
			itemData: data,
			uiActionConfig: action,
			itemIndex,
			requestData: {
				resourceTable: table,
				resourceId: recordId,
				currentId,
				currentTable,
				actionId: action.actionId,
				actionStage: 'pre_modal'
			},
			actionPath: action.restAPI,
			actionDisplayValue: action.displayValue || t('Submit')
		});
	}
};

export default restUtils;
