import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import {
	LOG,
	parseId,
	isAdditionalResource,
	isRecommendation,
	baseLinkFromRootDir,
	getAttachFieldObj,
	copyToClipboard,
	showTabMessage
} from '../../util/agentAssistUtils';
import { PREVIEW_RECORD } from '../../consts';
import attachUtils from '../../sn-aa-action-modal/viewUtils/attachUtils';
import linkUtils from '../../sn-aa-action-modal/viewUtils/linkUtils';
import restUtils from '../../sn-aa-action-modal/viewUtils/restUtils';
import { handleCopyResolution } from '../../sn-aa-action-modal/viewUtils/copyResolUtils';
import { AA_FLAG_ACTION } from '../../sn-aa-action-modal/viewUtils/flagUtils';
import { AA_UPDATE_REL_DOC } from '../../sn-aa-action-modal';
import { AA_PROPOSE_RECOMMENDATION } from '../../searchResultsContainer/resultListItem/sn-aa-action-footer/actions';
import { AA_CLOSE_RECOMMENDATION } from '../../searchResultsContainer/resultListItem/sn-aa-recommendation-record/actions';
import { AA_ACTION_CLICKED } from '../../sn-aa-action-header';
import { AA_UI_ACTION_CLICKED } from '../actions';
import { actionTypes } from '@servicenow/ui-core';

export const { COMPONENT_PROPERTY_CHANGED } = actionTypes;

function actionClickedEffect(coeffects) {
	const { dispatch, state } = coeffects;
	const { action, selected, item, itemIndex } = coeffects.action.payload;
	const {
		properties: {
			table: currentTable,
			sysId: currentId,
			fields: currentFields
		}
	} = state;
	const { actionId, value: actionValue } = action;
	const { table: resourceTable, sys_id: resourceId } = parseId(item.id);

	// TODO: Remove for catalog
	if (
		actionId === 'order' ||
		actionId === 'request' ||
		actionId === 'submit'
	) {
		let payload = {
			table: resourceTable,
			sys_id: resourceId,
			params: {
				sysparm_parent_table: currentTable,
				sysparm_parent_sys_id: currentId
			}
		};

		//TODO: move this to java
		const catalogItemLink = get(item, 'relatedLinks[0].link', '');
		const catalogItemSPLink = get(item, 'relatedLinks[0].spLink', '');
		if (
			catalogItemLink.includes('servicecatalog_cat_item_guide_view') ||
			catalogItemLink.includes('sysparm_guide') ||
			catalogItemSPLink.includes('sc_cat_item_guide')
		) {
			payload.table = 'sc_cat_item_guide';
		}

		dispatch(PREVIEW_RECORD, payload);
	}

	if (actionId === 'full_view') {
		dispatch(AA_UPDATE_REL_DOC, {
			itemData: item,
			itemIndex,
			actionValue,
			remove: selected
		});
		const source = get(item, 'meta.source', '');
		if (
			isRecommendation(item) ||
			isAdditionalResource(item) ||
			source === 'knowledge' ||
			source === 'pinned'
		) {
			const payload = {
				table: resourceTable,
				sys_id: resourceId,
				params: {
					sysparm_parent_table: currentTable,
					sysparm_parent_sys_id: currentId
				}
			};
			dispatch(PREVIEW_RECORD, payload);
		} else {
			// others full_view
			const linkToOpen = baseLinkFromRootDir(
				item.meta.full_view_url || item.link || item.meta.link
			);
			if (linkToOpen) {
				window.open(linkToOpen, '_blank');
			} else {
				LOG.error('Invalid knowledge link', linkToOpen);
			}
		}
	}

	if (actionId === 'flag' && !selected) {
		const actionConfirmModalProps = {
			show: true,
			data: item,
			remove: selected,
			action: actionValue
		};
		// this.setState({actionConfirmModalProps: actionConfirmModalProps});
		dispatch(AA_FLAG_ACTION, {
			actionConfirmModalProps: actionConfirmModalProps
		});
		return;
	}

	if (actionId === 'rest:copy_note_template') {
		copyToClipboard(item.meta.card_snippet).then(() => {
			showTabMessage(
				'info',
				t('Copied to clipboard!'),
				currentTable,
				currentId,
				dispatch
			);
		});
	}

	if (actionId.includes(':')) {
		const [actionType] = actionId.split(':');
		if (actionType === 'link' || actionType === 'link_to') {
			linkUtils.link({ action, data: item, itemIndex }, dispatch);
			return;
		}

		if (actionType === 'copy_resolution') {
			handleCopyResolution({ action, data: item, itemIndex }, dispatch);
			return;
		}

		if (actionType === 'rest') {
			restUtils.startRestAction(
				{ action, data: item, itemIndex, currentId, currentTable },
				dispatch
			);
			return;
		}
	}

	if (actionId === 'attach' && !selected) {
		const attachmentType = action.kbAttachment;
		const attachFieldObj = getAttachFieldObj(
			action,
			currentFields,
			currentTable
		);

		if (attachFieldObj) {
			if (isEmpty(attachFieldObj)) {
				//Logs the attach action without doing anything else
				dispatch(AA_UPDATE_REL_DOC, {
					itemData: item,
					itemIndex,
					actionValue,
					actionId,
					remove: selected
				});
			} else {
				attachUtils.fetchAttachment(
					{
						action,
						data: item,
						itemIndex,
						attachmentType,
						attachFieldObj
					},
					dispatch
				);
			}
		} else {
			showTabMessage(
				'warning',
				t('Attach note field not found'),
				currentTable,
				currentId,
				dispatch
			);
		}

		return;
	}
	//This handles all branches that are not returned: unattach, full_view, helpful, copy_note_template.
	dispatch(AA_UPDATE_REL_DOC, {
		itemData: item,
		itemIndex,
		actionValue,
		actionId,
		remove: selected
	});
}

function formTimeStampChangedEffect(coeffects) {
	const { state, updateState } = coeffects;
	updateState({
		...state,
		formTimeStamp: Date.now()
	});
}

function proposeRecommendationEffect(coeffects) {
	const { action, dispatch } = coeffects;
	const actionSysId = action.payload.proposeUIActionSysId;
	if (actionSysId) {
		dispatch(AA_UI_ACTION_CLICKED, { actionSysId });
	}
}

function closeRecommendationEffect(coeffects) {
	const { state, updateState } = coeffects;
	updateState({
		...state,
		recommendation: {
			...state.recommendation,
			show: false
		}
	});
}

const makeActionClickedHandler = () => ({
	effect: actionClickedEffect,
	stopPropagation: true
});

const formTimeStampChangedHandler = () => ({
	effect: formTimeStampChangedEffect,
	stopPropagation: true
});

const proposeRecommendationHandler = () => ({
	effect: proposeRecommendationEffect,
	stopPropagation: true
});

const closeRecommendationHandler = () => ({
	effect: closeRecommendationEffect,
	stopPropagation: true
});

export const actionUtils = {
	actionHandlers: {
		[AA_ACTION_CLICKED]: makeActionClickedHandler(),
		[AA_PROPOSE_RECOMMENDATION]: proposeRecommendationHandler(),
		[AA_CLOSE_RECOMMENDATION]: closeRecommendationHandler(),
		[COMPONENT_PROPERTY_CHANGED]: formTimeStampChangedHandler()
	}
};
