import isEmpty from 'lodash/isEmpty';
import {
	getItemActionsMap,
	isUibClassDecorator
} from '../util/agentAssistUtils';
import { DETAIL_VIEW } from '../consts';
import '../sn-aa-catalog-detail';
import '../sn-aa-response-template-detail';
import { t } from 'sn-translate';
import { AA_TOGGLE_DETAIL_VIEW } from './actions';
import '@servicenow/now-button';
import '../sn-aa-component-record-detail';
import '../sn-aa-action-header';
import 'sn-component-workspace-knowledge';
import { LOG } from '../util/agentAssistUtils';
import '../searchResultsContainer/sn-aa-results-footer';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';

function getItem(source, state) {
	const [table, sysId] = state.properties.item.id.split(':');
	const DetailComponentName =
		state.properties.item.meta.detail_component_name;
	const isUib = state.properties.isUib ? true : false;

	if (source === 'community_answer') {
		return (
			<sn-community-qa-detail
				component-id={
					state.properties.parentId + '_' + state.properties.item.id
				}
				id={state.properties.item.id}
				sys-id={sysId}
				table={table}
				frameless={true}
				is-uib={isUib}
			/>
		);
	}

	if (source === 'community_question') {
		return (
			<sn-community-qa-detail
				component-id={
					state.properties.parentId + '_' + state.properties.item.id
				}
				id={state.properties.item.id}
				sys-id={sysId}
				table={table}
				frameless={true}
				is-uib={isUib}
			/>
		);
	}

	if (source === 'community_blog') {
		return (
			<sn-community-blog-detail
				component-id={
					state.properties.parentId + '_' + state.properties.item.id
				}
				id={state.properties.item.id}
				sys-id={sysId}
				table={table}
				frameless={true}
				is-uib={isUib}
			/>
		);
	}

	if (!isEmpty(DetailComponentName)) {
		const pinned = source === 'pinned';
		return (
			<DetailComponentName
				component-id={
					state.properties.parentId + '_' + state.properties.item.id
				}
				id={state.properties.item.id}
				sys-id={sysId}
				table={table}
				fields={state.properties.formRecordInfo.currentFields}
				item={state.properties.item}
				pinned={pinned}
				frameless={true}
				is-uib={isUib}
			/>
		);
	}

	return (
		<sn-aa-record-detail
			item={state.properties.item}
			component-id={
				state.properties.parentId + '_' + state.properties.item.id
			}
			is-uib={isUib}
		/>
	);
}

const view = (state, dispatch) => {
	const { showNext, showPrev, item, isUib } = state.properties;
	if (!item) {
		LOG.warn('sn-aa-detail-container: item is undefined');
		return null;
	}
	const source = item.meta.source;
	const actions = getItemActionsMap(item, state.properties.actions)
		.headerActions;
	const actionStates = state.properties.actionStates[item.id];
	if (!actionStates) {
		LOG.warn('sn-aa-detail-container: actionStates is undefined');
		return null;
	}
	const hideCarouselBtns =
		state.properties.isRecommendation ||
		!state.properties.allowResultPreviewButtons;
	const footerNavBar =
		hideCarouselBtns || (!showPrev && !showNext) ? null : (
			<sn-aa-results-footer
				key="search-details-footer"
				{...state.properties}
			/>
		);
	return (
		<Fragment>
			<div
				className="aa-details-container"
				ref={el => (state.detailsContainerRef = el)}
			>
				<div
					className={isUibClassDecorator(isUib, 'aa-details-header')}
				>
					<div className="aa-details-header-content">
						<now-button
							type="Tertiary"
							icon-set="solid"
							icon-name="chevron-left-outline"
							bare
							size="md"
							on-click={() =>
								dispatch(AA_TOGGLE_DETAIL_VIEW, {
									showDetail: false,
									itemIndex: state.properties.itemIndex
								})
							}
							aria-label={t('Back')}
						/>
					</div>
					<div className="aa-details-header-content">
						<sn-aa-action-header
							view={DETAIL_VIEW}
							form-record-info={state.properties.formRecordInfo}
							item={state.properties.item}
							item-index={state.properties.itemIndex}
							source={source}
							actions={actions}
							visible-actions={actionStates.visibleActions}
							is-new-record={state.properties.isNewRecord}
							form-time-stamp={state.properties.formTimeStamp}
							is-recommendation={
								state.properties.isRecommendation
							}
						/>
					</div>
				</div>
				<div
					className="aa-details-body"
					aria-live="polite"
					tabIndex="-1"
				>
					{getItem(state.properties.item.meta.source, state)}
				</div>
			</div>
			{footerNavBar}
		</Fragment>
	);
};

export default view;
