import get from 'lodash/get';
import { t } from 'sn-translate';
import '@servicenow/now-dropdown';
import { SEARCHER, DEFAULT_RESOURCES_DISPLAY } from '../../consts';

const getDropdownItems = (filter) => {
	const searcherLabel = get(filter, 'searcherLabel', DEFAULT_RESOURCES_DISPLAY);
	const searcherResourceNames = get(filter, 'searcherResourceNames', []) || [];
	const additionalResNames = get(filter, 'additionalResNames', []) || [];
	const searcherGroup = { id: SEARCHER, label: searcherLabel };
	const searcherRes = searcherResourceNames.map((src) =>
		({ id: src.value, label: src.displayValue })
	);
	const additionalRes = additionalResNames.map((src) =>
		({ id: src.value, label: src.displayValue })
	);
	if (searcherRes.length + additionalRes.length <= 1)
		return null;

	const searcherResWithGroup = (searcherRes.length === 0) ? [] : [searcherGroup, ...searcherRes];
	const showSearcherFirst = get(filter, 'showSearcherFirst', false);
	return showSearcherFirst ?
		[...searcherResWithGroup, ...additionalRes] :
		[...additionalRes, ...searcherResWithGroup];
};

const view = (state) => {
	const {
		properties: { filter, isLoading, resourceSelectorState: {selectedResource} }
	} = state;
	let selectedList = [];
	selectedList.push(selectedResource);
	const dropdownItems = getDropdownItems(filter);
	if (dropdownItems === null)
		return null;
	return (
		<now-dropdown
			disabled={isLoading}
			icon="sliders-outline"
			size="md"
			hide-caret hide-label
			select="single"
			variant="tertiary"
			items={dropdownItems}
			tooltip-content={t("Select a search resource")}
			config-aria={{'aria-label': t('Select a search resource')}}
			selected-items={selectedList} />
	);
};

export default view;