import '@servicenow/now-modal';
import '@devsnc/sn-form-controls';
import attachUtils, { ATTACH_ACTION_ID } from './viewUtils/attachUtils';
import linkUtils, { LINK_TO, LINK } from './viewUtils/linkUtils';
import copyResolUtils, { COPYRES_ACTION_ID } from './viewUtils/copyResolUtils';
import restUtils, { REST_ACTION_ID } from './viewUtils/restUtils';
import flagUtils, { FLAG_ACTION_ID } from './viewUtils/flagUtils';

function getModalUtils(actionId = '') {
	switch(actionId) {
	case ATTACH_ACTION_ID: return attachUtils;
	case LINK:
	case LINK_TO: return linkUtils;
	case COPYRES_ACTION_ID: return copyResolUtils;
	case REST_ACTION_ID: return restUtils;
	case FLAG_ACTION_ID: return flagUtils;
	}
}

const view = (state, dispatch) => {
    
	const { properties: { actionId, modalState, show , currentFields }, userInputObj} = state;
	if(!actionId || !modalState || !modalState.hasOwnProperty(actionId) || !show)
		return null;
	const modalUtils = getModalUtils(actionId);
	return (
		<now-modal
			opened={show}
			size='lg'
			header-label={modalUtils.getHeaderLabel(modalState[actionId])}
			footer-actions={modalUtils.getFooterActions(userInputObj, modalState[actionId])}>
			{modalUtils.getInnerComponent(modalState[actionId], currentFields, userInputObj, dispatch)}
		</now-modal>
	);
};

export default view;