// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { DETAIL_VIEW_VISIBLE_ACTION_LIMIT } from '../consts';
import {
	// isAdditionalResource,
	isListView
	// parseId
} from '../util/agentAssistUtils';
import { t } from 'sn-translate';
import { AA_ACTION_CLICKED } from './actions';

const view = (state, dispatch) => {
	const { actions } = state.properties;
	if (!actions || actions.length === 0) {return null;}

	let mainActions = actions;
	let otherActions = actions;

	if (isListView(state.properties.view)) {
		if (state.actionsFiltered)
			{otherActions = actions.filter(action =>
				state.filteredActions.has(action.actionId)
			);}
	} else {
		if (state.properties.isRecommendation) {
			mainActions = [actions[0]];
			otherActions = actions.slice(1);
		} else {
			if (!state.actionsFiltered) {return null;}

			let filteredActions = actions.filter(action =>
				state.filteredActions.has(action.actionId)
			);
			mainActions = filteredActions.slice(
				0,
				DETAIL_VIEW_VISIBLE_ACTION_LIMIT
			);
			otherActions = filteredActions.slice(
				DETAIL_VIEW_VISIBLE_ACTION_LIMIT
			);
		}
	}
	return (
		<div className="aa-action-header">
			<div className="aa-action-header-content">
				{standaloneActions(state, mainActions, dispatch)}
			</div>
			<div className="aa-action-header-content">
				{dropdownMenu(state, otherActions)}
			</div>
		</div>
	);
};

function isSelected(visibleActions, action) {
	return visibleActions.has(action.value);
}

function getAction(state, action, selected, key, dispatch) {
	var displayText = isListView(state.properties.view)
		? action.badge
		: action.displayValue;
	const badgeIcon = 'check-circle-outline';

	if (isListView(state.properties.view) || state.properties.disabled)
		{return (
			<div
				key={key}
				className="aa-action-header-badge"
				aria-describedby={action.actionId}
			>
				<div
					className="aa-action-header-text"
					id={action.actionId}
					title={displayText}
				>
					{selected ? <now-icon icon={badgeIcon} /> : null}
					<span>{displayText}</span>
				</div>
			</div>
		);}

	// Actions in preview page
	const hasReverse = !!action.reverseDisplayValue;
	const shouldReverse = selected && hasReverse;
	displayText = shouldReverse
		? action.reverseDisplayValue
		: action.displayValue;

	return (
		<button
			type="button"
			key={key}
			className="aa-action-header-btn"
			on-click={() =>
				dispatch(AA_ACTION_CLICKED, {
					action,
					selected,
					item: state.properties.item,
					itemIndex: state.properties.itemIndex
				})
			}
			aria-label={displayText}
		>
			{displayText}
		</button>
	);
}

function standaloneActions(state, actions, dispatch) {
	let tags = null;

	if (isListView(state.properties.view))
		{actions = actions.filter(action =>
			isSelected(state.properties.visibleActions, action)
		);}

	tags = actions.map((action, i) =>
		getAction(
			state,
			action,
			isSelected(state.properties.visibleActions, action),
			'sa-action-text-' + i,
			dispatch
		)
	);

	if (!tags) {return null;}

	return <Fragment>{tags}</Fragment>;
}

function dropdownMenu(state, actions) {
	const dropdownItems = actions.map(action => {
		const selected = isSelected(state.properties.visibleActions, action);
		const hasReverse = !!action.reverseDisplayValue;
		const shouldReverse = selected && hasReverse;
		return {
			id: action.actionId,
			label: shouldReverse
				? action.reverseDisplayValue
				: action.displayValue,
			action: action,
			selected: selected
		};
	});
	const iconSize = isListView(state.properties.view) ? 'sm' : 'md';
	if (dropdownItems.length === 0) {return null;}
	return (
		<now-dropdown
			icon="ellipsis-v-outline"
			size={iconSize}
			hide-caret
			bare
			select="none"
			variant="secondary"
			items={dropdownItems}
			opened={state.dropdownOpened}
			config-aria={{ 'aria-label': t('actions on this record') }}
			manageOpened={true}
		/>
	);
}

export default view;
