import {
	AA_FOOTER_PREV_CLICKED,
	AA_FOOTER_NEXT_CLICKED
} from '../../searchResultsContainer/sn-aa-results-footer/actions';

import {
	AA_PAGE_CHANGE_SEARCH
} from '../actions';

const showFooterPrevClicked = ({ dispatch }) => {
	dispatch(AA_PAGE_CHANGE_SEARCH, { direction : -1});
};

const showFooterNextClicked = ({ dispatch }) => {
	dispatch(AA_PAGE_CHANGE_SEARCH, { direction : 1});
};

export const resultsContainerHandlers = {
	[AA_FOOTER_PREV_CLICKED]: showFooterPrevClicked,
	[AA_FOOTER_NEXT_CLICKED]: showFooterNextClicked
}; 