import { t } from 'sn-translate';
import { AA_FOOTER_NEXT_CLICKED, AA_FOOTER_PREV_CLICKED } from './actions';
import { isUibClassDecorator } from '../../util/agentAssistUtils';

export const view = (state, dispatch) => {
	const { showNext, showPrev, children, isUib } = state.properties;

	return (
		<footer className="aa-footer">
			<div
				className={isUibClassDecorator(
					isUib,
					'aa-footer-pagination-container'
				)}
			>
				<now-button
					bare
					disabled={!showPrev ? true : false}
					icon="arrow-left-outline"
					size="md"
					variant="tertiary"
					tooltip-content={t('Previous Page')}
					config-aria={{ 'aria-label': t('Previous Page') }}
					on-click={e => {
						e.stopPropagation();
						if (showPrev) {
							dispatch(AA_FOOTER_PREV_CLICKED, {});
						}
					}}
				/>
				{children}
				<now-button
					bare
					disabled={!showNext ? true : false}
					icon="arrow-right-outline"
					size="md"
					variant="tertiary"
					tooltip-content={t('Next Page')}
					config-aria={{ 'aria-label': t('Previous Page') }}
					on-click={e => {
						e.stopPropagation();
						if (showNext) {
							dispatch(AA_FOOTER_NEXT_CLICKED, {});
						}
					}}
				/>
			</div>
		</footer>
	);
};
