import get from 'lodash/get';
import escape from 'lodash/escape';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import makeUIActionRestEffect from './makeUIActionRestEffect';
import {
	setFormFields,
	replaceBadSpaces,
	showTabMessage
} from '../../util/agentAssistUtils';
import { AA_UPDATE_REL_DOC } from '../actions';
import { RESET_MODAL_STATE } from './uiActionCommonUtils';

export const AA_ATTACH_MODAL_SUBMIT = 'AGENT_ASSIST#ATTACH_MODAL_SUBMIT';
export const AA_ATTACH_MODAL_CANCEL = 'AGENT_ASSIST#ATTACH_MODAL_CANCEL';
export const AA_ATTACH_MODAL_USER_INPUT =
	'AGENT_ASSIST#ATTACH_ACT_MODAL_USER_INPUT';
export const ATTACH_ACTION_ID = 'attach'; //to match the action_id in cxs_ui_action
export const ATTACH_ACTION_PATH = 'attach'; //to match rest resource name
export const AA_FETCH_ATTACHMENT = 'AGENT_ASSIST#FETCH_ATTACHMENT';
export const AA_FETCH_ATTACHMENT_SUCCESS =
	'AGENT_ASSIST#FFETCH_ATTACHMENT_SUCCESS';
export const AA_FETCH_ATTACHMENT_ERROR = 'AGENT_ASSIST#FFETCH_ATTACHMENT_ERROR';
const ATTACH_ARTICLE_DISPLAY = t('Attach Article');
const CANCEL_DISPLAY = t('Cancel');

const attachUtils = {
	getInnerComponent(
		attachModalState = {},
		currentFields = {}, //eslint-disable-line no-unused-vars
		userInputObj = {},
		dispatch
	) {
		const _inputHandler = userInputObj => {
			dispatch(AA_ATTACH_MODAL_USER_INPUT, userInputObj);
		};

		const { article } = attachModalState.attachment; //TODO get
		const attachFieldLabel = get(
			attachModalState,
			'attachFieldObj.label',
			t('Add Comments')
		);
		const attachFieldName = get(
			attachModalState,
			'attachFieldObj.name',
			'comments'
		);
		const userInputValue = get(
			userInputObj,
			`${attachFieldName}.value`,
			''
		);
		//TODO pass in field object, and add field computation here.
		return (
			<Fragment>
				<div className="aa-modal--body">
					<div className="aa-card">
						<div className="aa-card--row">
							<div className="aa-card--title aa-card--title__lg">
								<span>{article.short_description}</span>
							</div>
							<div className="aa-card--body">
								<span>{article.number}</span>
							</div>
						</div>
					</div>
					<div className="aa-modal--input">
						<sn-record-input
							type="textarea"
							label={attachFieldLabel}
							value={userInputValue}
							maxlength="4000"
							onValueChange={(
								{ value, displayValue } //eslint-disable-line no-unused-vars
							) =>
								_inputHandler({
									[get(
										attachModalState,
										'attachFieldObj.name',
										'comments'
									)]: {
										value
									}
								})
							}
						/>
					</div>
				</div>
			</Fragment>
		);
	},

	getFooterActions(userInputObj = {}) {
		return [
			{
				variant: 'primary',
				label: ATTACH_ARTICLE_DISPLAY,
				clickActionType: AA_ATTACH_MODAL_SUBMIT,
				userInputObj
			},
			{
				variant: 'secondary',
				label: CANCEL_DISPLAY,
				clickActionType: RESET_MODAL_STATE
			}
		];
	},

	getHeaderLabel() {
		return ATTACH_ARTICLE_DISPLAY;
	},

	getInitialModalState() {
		return {
			[ATTACH_ACTION_ID]: {}
		};
	},
	actionHandlers: {
		modalInternalActions: {
			[AA_ATTACH_MODAL_USER_INPUT]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					updateState({
						...state,
						userInputObj: action.payload
					});
				},
				stopPropagation: true
			}
		},
		modalExternalActions: {
			//Fetch attachment
			[AA_FETCH_ATTACHMENT]: {
				...makeUIActionRestEffect(
					AA_FETCH_ATTACHMENT_SUCCESS,
					AA_FETCH_ATTACHMENT_ERROR
				),
				interceptors: [
					{
						before: context => ({
							...context,
							effects: [
								...context.effects,
								{
									//Store itemData uiActionConfig itemIndex for later use
									effect: coeffects => {
										const {
											state,
											action,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												[ATTACH_ACTION_ID]: {
													...state.modalState[
														ATTACH_ACTION_ID
													],
													itemData:
														action.payload.itemData,
													itemIndex:
														action.payload
															.itemIndex,
													uiActionConfig:
														action.payload
															.uiActionConfig,
													attachFieldObj:
														action.payload
															.attachFieldObj
												}
											}
										});
									},
									args: [context.coeffects]
								}
							]
						})
					}
				],
				stopPropagation: true
			},
			[AA_FETCH_ATTACHMENT_SUCCESS]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					const { payload } = action;
					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: true,
							activeModal: ATTACH_ACTION_ID,
							[ATTACH_ACTION_ID]: {
								...state.modalState.attach,
								attachment: payload.result
							}
						}
					});
				},
				stopPropagation: true
			},
			[AA_FETCH_ATTACHMENT_ERROR]: { stopPropagation: true },
			[AA_ATTACH_MODAL_SUBMIT]: {
				//dispatch to form, update relevant doc, and clear modalState, show message.
				effect: coeffects => {
					const {
						state: {
							modalState: {
								[ATTACH_ACTION_ID]: {
									uiActionConfig,
									attachment,
									itemData,
									itemIndex,
									attachFieldObj
								}
							},
							properties: {
								table: currentTable,
								sysId: currentSysId
							}
						} = {
							modalState: {
								[ATTACH_ACTION_ID]: {
									uiActionConfig: {},
									attachment: {},
									itemData: {},
									attachFieldObj: {}
								}
							},
							properties: {
								table: '',
								sysId: ''
							}
						},
						action: {
							payload: {
								footerAction: { userInputObj }
							}
						} = {
							payload: {
								footerAction: { userInputObj: null }
							}
						},
						dispatch
					} = coeffects;
					if (attachFieldObj && attachFieldObj.name) {
						//send update rel
						dispatch(AA_UPDATE_REL_DOC, {
							itemData,
							itemIndex,
							actionValue: uiActionConfig.value,
							remove: false
						});

						//add informessage
						showTabMessage(
							'info',
							'<span>' +
								t(
									'Details of {0} have been added to the {1} field',
									escape(itemData.title),
									attachFieldObj.label
								) +
								'</span>',
							currentTable,
							currentSysId,
							dispatch
						);

						//add to form field and save
						const fieldValue = replaceBadSpaces(
							get(
								userInputObj,
								`${attachFieldObj.name}.value`,
								''
							) +
								'\n' +
								attachment.article.content
						);
						setFormFields(
							{
								[attachFieldObj.name]: {
									value: fieldValue,
									displayValue: fieldValue
								}
							},
							true, // save form
							dispatch
						);

						//reset modalState
						dispatch(RESET_MODAL_STATE);
					}
				},
				stopPropagation: true
			},
			[AA_ATTACH_MODAL_CANCEL]: {
				effect: coeffects => {
					const { state, updateState } = coeffects;
					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: false,
							activeModal: '',
							[ATTACH_ACTION_ID]: {}
						}
					});
				},
				stopPropagation: true
			}
		}
	},
	fetchAttachment: (
		{ action, data, itemIndex, attachmentType, attachFieldObj },
		dispatch
	) => {
		dispatch(AA_FETCH_ATTACHMENT, {
			itemData: data,
			uiActionConfig: action,
			itemIndex,
			attachFieldObj,
			requestData: {
				relevant_doc: data.id,
				attachment_type: attachmentType
			},
			actionPath: ATTACH_ACTION_PATH
		});
	}
};

export default attachUtils;
