import get from 'lodash/get';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import makeUIActionRestEffect from './makeUIActionRestEffect';
import {
	fieldEditable,
	setFormFields,
	// replaceBadSpaces,
	showTabMessage,
	parseId,
	isRecommendation
} from '../../util/agentAssistUtils';
import { AA_UPDATE_REL_DOC } from '../actions';
import { RESET_MODAL_STATE } from './uiActionCommonUtils';

export const AA_COPYRES_MODAL_SUBMIT = 'AGENT_ASSIST#COPYRES_MODAL_SUBMIT';
export const AA_COPYRES_MODAL_CANCEL = 'AGENT_ASSIST#COPYRES_MODAL_CANCEL';
export const AA_COPYRES_MODAL_USER_INPUT =
	'AGENT_ASSIST#COPYRES_ACT_MODAL_USER_INPUT';
export const COPYRES_ACTION_ID = 'copy_resolution'; //to match the action_id in cxs_ui_action
export const AA_FETCH_RESOL_DETAIL = 'AGENT_ASSIST#FETCH_RESOL_DETAIL';
export const AA_FETCH_RESOL_DETAIL_RECOMMENDATION =
	'AGENT_ASSIST#FETCH_RESOL_DETAIL_RECOMMENDATION';
export const AA_FETCH_RESOL_DETAIL_SUCCESS =
	'AGENT_ASSIST#FETCH_RESOL_DETAIL_SUCCESS';
export const AA_FETCH_RESOL_DETAIL_ERROR =
	'AGENT_ASSIST#FETCH_RESOL_DETAIL_ERROR';
export const COPY_RESOLUTION_DISPLAY = t('Copy Resolution');
//TODO multi export vs single export
const copyResolUtils = {
	getInnerComponent(
		copyResolModalState = {},
		currentFields = {},
		userInputObj = {},
		dispatch
	) {
		const _inputHandler = userInputObj => {
			dispatch(AA_COPYRES_MODAL_USER_INPUT, userInputObj);
		};

		const { resolDetails, meta } = copyResolModalState;
		//close_code might not be available for case
		const resourceNumber = get(meta, 'number.display_value', null);
		const currentNumber = get(currentFields, 'number.displayValue', null);
		let alertText = null;
		if (resourceNumber && currentNumber) {
			alertText = t(
				'You are applying the resolution code and notes from {0} to {1}.',
				resourceNumber,
				currentNumber
			);
		} else if (currentNumber) {
			alertText = t(
				'You are applying the resolution code to {0}.',
				currentNumber
			);
		}

		const resolutionInputs = Object.keys(resolDetails)
			.map(key => {
				if (
					!key ||
					typeof key !== 'string' ||
					!currentFields.hasOwnProperty(key) //eslint-disable-line
				) {
					return null;
				}

				// note the difference between userInputObj and resolDetails - resolDetails comes from rest response while userInputObj has value only after user provided inputs
				if (key.includes('_code')) {
					return (
						<div className="aa-modal--input" key={key}>
							<sn-record-choice
								label={currentFields[key].label}
								value={get(
									userInputObj,
									`${key}.value`,
									resolDetails[key].value
								)}
								display-value={get(
									userInputObj,
									`${key}.displayValue`,
									resolDetails[key].display_value
								)}
								choices={currentFields[key].choices}
								onValueChange={({ value, displayValue }) => {
									_inputHandler({
										[key]: {
											value,
											displayValue
										}
									});
								}}
							/>
						</div>
					);
				} else {
					return (
						<div className="aa-modal--input" key={key}>
							<sn-record-input
								type="textarea"
								maxlength="4000"
								label={currentFields[key].label}
								value={get(
									userInputObj,
									`${key}.value`,
									get(resolDetails, `${key}.value`, '')
								)}
								display-value={get(
									userInputObj,
									`${key}.displayValue`,
									get(
										resolDetails,
										`${key}.display_value`,
										''
									)
								)}
								onValueChange={({ value, displayValue }) => {
									_inputHandler({
										[key]: {
											value,
											displayValue
										}
									});
								}}
							/>
						</div>
					);
				}
			})
			.filter(input => !!input);

		if (!resolutionInputs || resolutionInputs.length < 1) {
			alertText = t('Resolution fields not available.');
		}

		const alert = alertText ? (
			<now-alert
				expanded="false"
				manageExpanded="false"
				status="info"
				icon-name="circle-info-outline"
				content={alertText}
			></now-alert>
		) : null;
		return (
			<Fragment>
				<div className="aa-modal--body">
					{alert}
					{resolutionInputs}
					<div className="aa-modal--input">
						<sn-record-input
							type="textarea"
							label={currentFields['work_notes'].label}
							value={get(userInputObj, 'work_notes.value', '')}
							maxlength="4000"
							onValueChange={({ value }) => {
								_inputHandler({
									work_notes: {
										value,
										displayValue: value
									}
								});
							}}
						/>
					</div>
					<div className="aa-modal--input">
						<sn-record-input
							type="textarea"
							label={currentFields['comments'].label}
							value={get(userInputObj, 'comments.value', '')}
							maxlength="4000"
							onValueChange={({ value }) => {
								_inputHandler({
									comments: {
										value,
										displayValue: value
									}
								});
							}}
						/>
					</div>
				</div>
			</Fragment>
		);
	},

	getFooterActions(userInputObj = {}) {
		return [
			{
				variant: 'primary',
				label: COPY_RESOLUTION_DISPLAY,
				clickActionType: AA_COPYRES_MODAL_SUBMIT,
				userInputObj
			},
			{
				variant: 'secondary',
				label: t('Cancel'),
				clickActionType: RESET_MODAL_STATE
			}
		];
	},

	getHeaderLabel() {
		return COPY_RESOLUTION_DISPLAY;
	},

	getInitialModalState() {
		return {
			[COPYRES_ACTION_ID]: {}
		};
	},
	actionHandlers: {
		modalInternalActions: {
			[AA_COPYRES_MODAL_USER_INPUT]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					updateState({
						...state,
						userInputObj: {
							...state.userInputObj,
							...action.payload
						}
					});
				},

				stopPropagation: true
			}
		},
		modalExternalActions: {
			[AA_FETCH_RESOL_DETAIL]: {
				...makeUIActionRestEffect(
					AA_FETCH_RESOL_DETAIL_SUCCESS,
					AA_FETCH_RESOL_DETAIL_ERROR
				),
				interceptors: [
					{
						before: context => ({
							...context,
							effects: [
								...context.effects,
								{
									//Store itemData uiActionConfig itemIndex for later use
									effect: coeffects => {
										const {
											state,
											action,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												[COPYRES_ACTION_ID]: {
													...state.modalState[
														COPYRES_ACTION_ID
													],
													itemData:
														action.payload.itemData,
													itemIndex:
														action.payload
															.itemIndex,
													uiActionConfig:
														action.payload
															.uiActionConfig
												}
											}
										});
									},
									args: [context.coeffects]
								}
							]
						})
					}
				],
				stopPropagation: true
			},
			[AA_FETCH_RESOL_DETAIL_SUCCESS]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					const { payload } = action;
					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: true,
							activeModal: COPYRES_ACTION_ID,
							[COPYRES_ACTION_ID]: {
								...state.modalState[COPYRES_ACTION_ID],
								infoMessage: payload.result.info_message,
								resolDetails: payload.result.payload,
								meta: payload.result.meta
							}
						}
					});
				},

				stopPropagation: true
			},
			[AA_FETCH_RESOL_DETAIL_SUCCESS]: {
				effect: () => {},
				args: [],
				interceptors: [
					{
						before: context => {
							const {
								coeffects: {
									action: {
										payload: {
											result: { payload: copyInfo }
										} = { result: { payload: {} } },
										payload
									},
									action, //eslint-disable-line no-unused-vars
									dispatch,
									state: {
										properties: { fields, table, sysId }
									}
								},
								coeffects
							} = context;

							let allFieldsAvailable = true;
							let newEffect = {};
							const missingFields = Object.keys(copyInfo)
								.map(key => {
									if (!fieldEditable(key, fields)) {
										allFieldsAvailable = false;
										return fields[key]
											? fields[key].label
											: key;
									}
									return null;
								})
								.filter(item => item !== null);
							if (allFieldsAvailable) {
								newEffect = {
									effect: coeffects => {
										const {
											state,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												active: true,
												activeModal: COPYRES_ACTION_ID,
												[COPYRES_ACTION_ID]: {
													...state.modalState[
														COPYRES_ACTION_ID
													],
													infoMessage:
														payload.result
															.info_message,
													resolDetails:
														payload.result.payload,
													meta: payload.result.meta
												}
											}
										});
									},
									args: [coeffects]
								};
							} else {
								newEffect = {
									effect: (
										missingFields,
										table,
										sysId,
										dispatch
									) => {
										const errorMessage = t(
											'Copy Resolution action is unavailable because following fields are not present on current record form: {0}',
											missingFields.join(',')
										);
										showTabMessage(
											'error',
											errorMessage,
											table,
											sysId,
											dispatch
										);
										return;
									},
									args: [
										missingFields,
										table,
										sysId,
										dispatch
									]
								};
							}
							return {
								...context,
								effects: [...context.effects, newEffect]
							};
						}
					}
				],
				stopPropagation: true
			},
			[AA_FETCH_RESOL_DETAIL_ERROR]: { stopPropagation: true },
			[AA_FETCH_RESOL_DETAIL_RECOMMENDATION]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					const { payload } = action;

					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: true,
							activeModal: COPYRES_ACTION_ID,
							[COPYRES_ACTION_ID]: {
								...state.modalState[COPYRES_ACTION_ID],
								infoMessage: t('Resolution details are copied'),
								resolDetails: payload.resolDetails,
								uiActionConfig: payload.uiActionConfig,
								itemIndex: payload.itemIndex,
								itemData: payload.itemData
							}
						}
					});
				},
				stopPropagation: true
			},
			//Modal buttons:
			[AA_COPYRES_MODAL_SUBMIT]: coeffects => {
				//dispatch to form, update relevant doc, and clear modalState, show message.

				const {
					state: {
						modalState: {
							[COPYRES_ACTION_ID]: {
								uiActionConfig,
								itemData,
								itemIndex,
								infoMessage,
								resolDetails
							}
						},
						properties: { table: currentTable, sysId: currentSysId }
					} = {
						modalState: {
							[COPYRES_ACTION_ID]: {
								uiActionConfig: {},
								itemData: {},
								infoMessage: '',
								resolDetails: {}
							}
						},
						properties: {
							table: '',
							sysId: ''
						}
					},
					action: {
						payload: {
							footerAction: { userInputObj }
						}
					} = {
						payload: {
							footerAction: { userInputObj: null }
						}
					},
					dispatch
				} = coeffects;

				//send update rel
				dispatch(AA_UPDATE_REL_DOC, {
					itemData,
					itemIndex,
					actionValue: uiActionConfig.value,
					remove: false
				});
				//add informessage
				showTabMessage(
					'info',
					infoMessage,
					currentTable,
					currentSysId,
					dispatch
				);
				//add to form field and save
				//userInputObj, if present, will override resolDetails
				//TODO replace bad space for work_notes and comments
				// const fieldValue =  replaceBadSpaces(userInputObj);
				setFormFields(
					{
						...resolDetails,
						...userInputObj
					}, //TODO refactor try to generalize and combine resolDetail and userInput
					true,
					dispatch
				);

				//reset modalState
				dispatch(RESET_MODAL_STATE);
			},
			[AA_COPYRES_MODAL_CANCEL]: {
				effect: coeffects => {
					const { state, updateState } = coeffects;
					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: false,
							activeModal: '',
							[COPYRES_ACTION_ID]: {}
						}
					});
				},
				stopPropagation: true
			}
		}
	}
};

const copyResolutionForRecommendation = function(
	{ action, data, itemIndex },
	dispatch
) {
	const commonField = get(
		data,
		'meta.recommendation_common_field.common_field',
		''
	);
	const commonFieldValue = get(data, 'meta.recommendation_result.value', '');
	const commonFieldDisplayValue = get(
		data,
		'meta.recommendation_result.display_value',
		''
	);
	const resolDetails = {
		[commonField]: {
			value: commonFieldValue,
			display_value: commonFieldDisplayValue
		},
		close_notes: {
			value: '',
			display_value: ''
		}
	};
	dispatch(AA_FETCH_RESOL_DETAIL_RECOMMENDATION, {
		itemData: data,
		uiActionConfig: action,
		itemIndex,
		resolDetails
	});
};

const fetchResolDetail = function({ action, data, itemIndex }, dispatch) {
	const { actionId } = action;
	const [, actionTargetTable] = actionId.split(':');
	const actionPath = `copy_${actionTargetTable}_resolution`;
	const parsed = parseId(data.id);
	const requestData = {
		resourceId: parsed.sys_id,
		actionId: actionId
	};

	dispatch(AA_FETCH_RESOL_DETAIL, {
		itemData: data,
		uiActionConfig: action,
		itemIndex,
		requestData,
		actionPath: actionPath
	});
};

export const handleCopyResolution = function(
	{ action, data, itemIndex },
	dispatch
) {
	if (isRecommendation(data)) {
		copyResolutionForRecommendation({ action, data, itemIndex }, dispatch);
	} else {
		fetchResolDetail({ action, data, itemIndex }, dispatch);
	}
};

export default copyResolUtils;
