import get from 'lodash/get';
import {
	RECOMMENDATION_HEADER_ICON,
	DEFAULT_RECOMMENDATION_HEADER_TITLE
} from '../../../consts';
import { AA_CLOSE_RECOMMENDATION } from './actions';
import { t } from 'sn-translate';
import '../sn-aa-result-list-item';
import '../sn-aa-action-footer';

export const RECOMMENDATION_DEFAULT_ID = 'recommendation_default_id';

// TODO : AA_CLOSE_RECOMMENDATION needs to be handled in snComponentWorkspaceAgentAssist
const recommendationHeader = (data, dispatch) => {
	return (
		<h3 className="aa-recommendation-header" id={data.id}>
			<div key={data.id} className="aa-recommendation-header-content">
				<now-icon icon={RECOMMENDATION_HEADER_ICON} size="md" />
				<span
					key={data.id + '_div'}
					className="aa-recommendation-header-title"
				>
					{data.meta.recommendation_title ||
						DEFAULT_RECOMMENDATION_HEADER_TITLE}
				</span>
			</div>
			<div className="aa-recommendation-header-content -static">
				<now-button
					iconName="close-outline"
					size="md"
					variant="tertiary"
					bare={true}
					on-click={() => dispatch(AA_CLOSE_RECOMMENDATION)}
					config-aria={{ 'aria-label': t('Close recommendation') }}
				/>
			</div>
		</h3>
	);
};

function isDetection(data) {
	return get(data, 'meta.recommendation_type', '') === 'detection';
}

const recommendationBody = (
	data,
	itemIndex,
	formRecordInfo,
	actions,
	actionStates,
	isNewRecord,
	source,
	formTimeStamp
) => {
	return isDetection(data) ? (
		<sn-aa-result-list-item
			key={data.id}
			data={data}
			source={source}
			form-record-info={formRecordInfo}
			item-index={itemIndex}
			action-states={actionStates}
			is-new-record={isNewRecord}
			actions={actions.headerActions}
			formTimeStamp={formTimeStamp}
		/>
	) : (
		<article
			className={'aa-recommendation-body'}
			on-click={e => {
				e.stopPropagation();
			}}
			tabIndex="0"
			role="link"
		>
			{get(data, 'meta.recommendation_message', '')}
		</article>
	);
};

const recommendationFooter = (
	data,
	actions,
	actionStates,
	itemIndex,
	formRecordInfo
) => {
	const primaryAction = actions.footerActions[0];
	return (
		<sn-aa-action-footer
			key={data.id}
			data={data}
			item-index={itemIndex}
			form-record-info={formRecordInfo}
			action={primaryAction}
			visible-actions={actionStates.visibleActions}
			is-detection={isDetection(data)}
		/>
	);
};

export const view = (state, dispatch) => {
	const {
		data,
		itemIndex,
		formRecordInfo,
		actions,
		actionStates,
		isNewRecord,
		source,
		formTimeStamp
	} = state.properties;

	return (
		<div className="aa-recommendation">
			{recommendationHeader(data, dispatch)}
			{recommendationBody(
				data,
				itemIndex,
				formRecordInfo,
				actions,
				actionStates,
				isNewRecord,
				source,
				formTimeStamp
			)}
			{recommendationFooter(
				data,
				actions,
				actionStates,
				itemIndex,
				formRecordInfo
			)}
		</div>
	);
};
