import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import cloneDeep from 'lodash/cloneDeep';
import SearchGraphQLQuery, { searchVariableList } from './searchGraphqlQuery';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { actionStateUtils } from './actionStateActionHandlers';
import { WORKSPACE, SEARCHER } from '../../consts';
import { AA_CONFIG_IN_STATE } from '../actions';
import { AA_SEARCH_BOX_CONTENT_CHANGED } from '../../searchControlsContainer/sn-aa-search-box/index';
import { AA_SEARCH_AS_CHANGE_SEARCH } from '../../searchControlsContainer/sn-aa-search-as/index';
import { AA_RESOURCE_CHANGE_SEARCH } from '../../searchControlsContainer/sn-aa-search-filter/index';
import {
	LOG,
	extractRecommendationResult,
	isRecommendation,
	isEmptySearchTermAllowed
} from '../../util/agentAssistUtils';

//TODO move actions to place of dispatch
export const AA_FORM_SEARCH_AS_FIELD_CHANGED =
	'AGENT_ASSIST#FORM_SEARCH_AS_FIELD_CHANGED';
export const AA_FORM_SEARCH_FIELD_CHANGED =
	'AGENT_ASSIST#FORM_SEARCH_FIELD_CHANGED';
export const AA_PAGE_CHANGE_SEARCH = 'AGENT_ASSIST#PAGE_CHANGE_SEARCH';
export const AA_SEARCH = 'AGENT_ASSIST#SEARCH';
export const AA_SEARCH_STARTED = 'AGENT_ASSIST#SEARCH_STARTED';
export const AA_SEARCH_SUCCEEDED = 'AGENT_ASSIST#SEARCH_SUCCEEDED';
export const AA_SEARCH_FAILED = 'AGENT_ASSIST#SEARCH_FAILED';
export const AA_UPDATE_SEARCH_STATE = 'AGENT_ASSIST#UPDATE_SEARCH_STATE';
export const AA_UPDATE_SEARCH_STATE_AND_ACTION_STATE =
	'AGENT_ASSIST#UPDATE_SEARCH_STATE_AND_ACTION_STATE';

export function buildBaseSearchRequestPayload(state, options = {}) {
	const { searchAsState, searchState } = state;
	const sourceField = !isEmpty(options.sourceField)
		? options.sourceField
		: !isEmpty(searchState.sourceField)
		? searchState.sourceField
		: searchState.formSearchField;
	const currentSearchTerm = searchState.currentSearchTerm;

	return {
		formSysID: state.properties.sysId,
		formTable: state.properties.table,
		tableConfig: state.properties.cxsTableConfig,
		isNewRecord: state.properties.isNewRecord || false,
		context: state.configs.context,
		session: state.properties.sysId,
		limit: state.configs.limit,
		maxLimit: state.configs.maxLimit,
		query: currentSearchTerm,
		sourceField,
		offset: 0, //Search from search text/resource/searchas change should start from first page
		originalQuery: '',
		resources: state.resourceSelectorState.resources,
		searchAs:
			searchAsState.isSearcherSelected && searchAsState.selected
				? searchAsState.user.value
				: '',
		includeRecommendation: !(
			state.properties.isNewRecord || state.recommendation.item
		),
		recommendationOnly: false,
		includePinnedArticles: true,
		user: 'javascript:gs.getUserID()',
		uiType: WORKSPACE,
		logSearch: false, //reference PRB1311989 + STRY50009545
		form: JSON.stringify({
			tn: state.properties.table,
			sys_id: state.properties.sysId,
			serialized: _serializeFormFields(state)
		}),
		...options
	};
}

function _buildSearchRequestPayload(state, options) {
	// When configuration is inactive, then we shouldn't allow search.
	if (!state.configs.active) {
		LOG.warn('Configuration is inactive, aborting search');
		return null;
	}

	const allowEmptySearchTerm = isEmptySearchTermAllowed(state);
	const payload = buildBaseSearchRequestPayload(state, options);
	if (!allowEmptySearchTerm && !payload.query) {
		LOG.warn('search term is empty, aborting search');
		return null;
	}

	return payload;
}

/**
 * Function description: convert 'state.properties.fields' to the format that can be further used to build encoded query in Java side and it is aligned with g_form.serialized on platform.
 * @param {object} state
 * @return {str} fieldsSerialized
 */
function _serializeFormFields(state) {
	let fieldsSerialized = '';
	const fields = { ...state.properties.fields };
	const tableName = state.properties.table;
	for (const field in fields) {
		if (typeof fields[field].value !== 'undefined') {
			fieldsSerialized =
				fieldsSerialized +
				'&' +
				tableName +
				'.' +
				field +
				'=' +
				fields[field].value;
		}
		if (typeof fields[field].displayValue !== 'undefined') {
			fieldsSerialized =
				fieldsSerialized +
				'&' +
				'sys_display.' +
				tableName +
				'.' +
				field +
				'=' +
				fields[field].displayValue;
		}
	}
	return fieldsSerialized
		.substring(1) // substring is used to remove the first '&'.
		.trim()
		.replace(/\s/g, '+');
}

function excludeRecommendation(searchResults) {
	// We should always return the copy, instead of the reference.
	if (searchResults && searchResults.length !== 0) {
		return isRecommendation(searchResults[0])
			? searchResults.slice(1)
			: searchResults.slice(0);
	}

	// When searchResults is empty array, [], then we still need to return the copy, instead of the reference.
	return searchResults.slice(0);
}

export const searchUtils = {
	actions: {
		[AA_CONFIG_IN_STATE]: {
			private: true
		},
		[AA_RESOURCE_CHANGE_SEARCH]: {
			private: true
		},
		[AA_SEARCH_BOX_CONTENT_CHANGED]: {
			private: true
		},
		[AA_PAGE_CHANGE_SEARCH]: {
			private: true
		},
		[AA_SEARCH_AS_CHANGE_SEARCH]: {
			private: true
		},
		[AA_SEARCH]: {
			private: true
		},
		[AA_SEARCH_STARTED]: {
			private: true
		},
		[AA_SEARCH_SUCCEEDED]: {
			private: true
		},
		[AA_UPDATE_SEARCH_STATE]: {
			private: true
		},
		[AA_UPDATE_SEARCH_STATE_AND_ACTION_STATE]: {
			private: true
		}
	},

	actionHandlers: {
		//Initial Search
		[AA_CONFIG_IN_STATE]: coeffects => {
			const { dispatch, state, updateState } = coeffects;
			const payload = _buildSearchRequestPayload(state, {
				isInitialSearch: true
			});

			if (payload) {
				dispatch(AA_SEARCH, payload);
			}
			// At this moment, configs.loading is still true, so we need to set it back to false if we stop searching. We show "No Matches Found" only when both configs.loading and searchState.isSearching are false.
			// Affected case: a new record with an empty short description (empty search field) should first see "Loading Search Results...", and then show "No Matches found".
			else {
				updateState({
					...state,
					configs: {
						...state.configs,
						loading: false
					}
				});
			}
		},
		//selected resource changed
		[AA_RESOURCE_CHANGE_SEARCH]: coeffects => {
			const {
				dispatch,
				state,
				action: {
					payload: { resourceSysId }
				},
				updateState
			} = coeffects;
			//TODO KL duplicate code in searchControlsContainerUtils
			const selectedSearcher = resourceSysId === SEARCHER;
			const selectedResourceInSearcher = state.configs.searcherResourceNames.some(
				src => src.value === resourceSysId
			);
			updateState({
				...state,
				resourceSelectorState: {
					...state.resourceSelectorState,
					active: true,
					selectedResource: resourceSysId,
					resources: selectedSearcher ? [] : [resourceSysId]
				},
				//Search As option only shows for searcher resources
				searchAsState: {
					...state.searchAsState,
					isSearcherSelected:
						selectedSearcher || selectedResourceInSearcher
							? true
							: false,
					selected:
						selectedSearcher || selectedResourceInSearcher
							? state.searchAsState.selected
							: false //When selected non-searcher resource, reset searchAs selected.
				},
				searchState: {
					...state.searchState,
					isInitialSearch: false
				}
			});
			const resources = resourceSysId === SEARCHER ? [] : [resourceSysId];
			const payload = _buildSearchRequestPayload(state, {
				resources: resources
			});
			if (payload) {
				dispatch(AA_SEARCH, payload);
			}
		},
		//Search box content changed
		[AA_SEARCH_BOX_CONTENT_CHANGED]: coeffects => {
			const { dispatch, state, action, updateState } = coeffects;
			const searchBoxValue = action.payload.searchBoxValue.trim();
			const currentSearchTerm = state.searchState.currentSearchTerm;

			//TODO check for bad space
			let requestPayload = null;
			// varialbes for state
			let newSourceField = 'additional_search';
			let newCurrentSearchTerm = searchBoxValue;
			let originalQuery = state.searchState.formSearchTerm;
			let logSearch = true;

			if (!searchBoxValue) {
				//if search text is empty, use form to search
				newSourceField = state.searchState.formSearchField;
				newCurrentSearchTerm = state.searchState.formSearchTerm;
				originalQuery = '';
				logSearch = false;
			}

			if (currentSearchTerm === newCurrentSearchTerm) {
				return;
			}

			updateState({
				...state,
				searchState: {
					...state.searchState,
					isInitialSearch: false,
					searchBoxText: searchBoxValue || '',
					sourceField: newSourceField,
					currentSearchTerm: newCurrentSearchTerm
				}
			});

			requestPayload = _buildSearchRequestPayload(state, {
				query: newCurrentSearchTerm,
				originalQuery: originalQuery,
				sourceField: newSourceField,
				logSearch: logSearch
			});

			if (requestPayload) {
				dispatch(AA_SEARCH, requestPayload);
			}
		},
		[AA_FORM_SEARCH_FIELD_CHANGED]: coeffects => {
			const { dispatch, state, action, updateState } = coeffects;
			// const {searchState: {searchBoxText}} = state;

			//new values for search state
			let newFormSearchField = action.payload.formSearchField;
			let newFormSearchTerm = action.payload.formSearchTerm;
			let newCurrentSearchTerm = state.searchState.currentSearchTerm;

			if (state.searchState.sourceField !== 'additional_search') {
				newCurrentSearchTerm = newFormSearchTerm;
			}

			updateState({
				...state,
				currentFields: cloneDeep(action.payload.newFields),
				searchState: {
					...state.searchState,
					isInitialSearch: false,
					formSearchField: newFormSearchField,
					formSearchTerm: newFormSearchTerm,
					currentSearchTerm: newCurrentSearchTerm || ''
				}
			});

			// Dispatch form field change search only if the user hasn't manually entered a search string in the search box
			if (state.searchState.sourceField === 'additional_search') {
				return;
			}

			const payload = _buildSearchRequestPayload(state, {
				query: newCurrentSearchTerm,
				sourceField: newFormSearchField
			});
			if (payload) {
				dispatch(AA_SEARCH, payload);
			}
		},
		[AA_PAGE_CHANGE_SEARCH]: coeffects => {
			const { dispatch, state, action, updateState } = coeffects;
			const oldOffset = state.searchState.offset;
			const resultsPerPage = state.configs.limit;
			const direction = action.payload.direction;
			const newOffset = oldOffset + resultsPerPage * direction;

			updateState({
				...state,
				searchState: {
					...state.searchState,
					isInitialSearch: false
				}
			});

			const payload = _buildSearchRequestPayload(state, {
				offset: newOffset
			});

			dispatch(AA_SEARCH, payload);
		},
		// Once the reference field of "search as" gets updated, we need to update searchAsState, and then re-do the search if search as toggled.
		// This action will be called only when search as is configured.
		[AA_FORM_SEARCH_AS_FIELD_CHANGED]: coeffects => {
			const { dispatch, state, action, updateState } = coeffects;
			const newUserDisplayValue = action.payload.newUserDisplayValue;
			const newUserSysId = action.payload.newUserSysId;
			// Update searchAsState because search as reference field get changed
			updateState({
				...state,
				searchAsState: {
					...state.searchAsState,
					user: {
						displayValue: newUserDisplayValue,
						value: newUserSysId
					}
				},
				searchState: {
					...state.searchState,
					isInitialSearch: false
				}
			});
			// Re-do the search when search as toggle is on and search as reference field is not empty
			const searchAsIsToggled = get(
				state,
				'searchAsState.selected',
				false
			);
			if (!isEmpty(newUserSysId) && searchAsIsToggled) {
				dispatch(AA_SEARCH_AS_CHANGE_SEARCH, {
					searchAsUser: newUserSysId
				});
			}
		},
		//search as toggled
		[AA_SEARCH_AS_CHANGE_SEARCH]: coeffects => {
			const { dispatch, state, action } = coeffects;
			//TODO KL searchControlsContainerUtils duplicate code?
			// console.log(AA_SEARCH_AS_CHANGE_SEARCH, coeffects);
			//TODO empty search, and update state

			const payload = _buildSearchRequestPayload(state, {
				searchAs: action.payload.searchAsUser
			});
			//TODO updateState is done in actionHandlerSearchContrlsContainer...
			if (payload) {
				dispatch(AA_SEARCH, payload);
			}
		},
		//Search GraphQL request
		[AA_SEARCH]: createGraphQLEffect(SearchGraphQLQuery, {
			variableList: searchVariableList,
			startActionType: AA_SEARCH_STARTED,
			successActionType: AA_SEARCH_SUCCEEDED,
			errorActionType: AA_SEARCH_FAILED
		}),
		[AA_SEARCH_STARTED]: coeffects => {
			const { state, updateState } = coeffects;
			updateState({
				...state,
				searchState: {
					...state.searchState,
					isSearching: true
				}
			});
		},
		[AA_SEARCH_SUCCEEDED]: coeffects => {
			const { state, action, updateState } = coeffects;
			// console.log(AA_SEARCH_SUCCEEDED + ' action.payload', action.payload);
			//If GraphQL succeeded, but search is errored
			//If there is partial data, we will proceed with error logged.
			if (action.payload.errors && action.payload.errors.length > 0) {
				LOG.error(
					'Search returned with ' +
						action.payload.errors.length +
						' error(s)',
					action.payload.errors
				);
			}

			if (
				!action.payload.data ||
				!action.payload.data.GlideContextualSearch_Query
			) {
				LOG.error(
					'Search returned with empty data: ',
					action.payload.data
				);
				return;
			}

			const searchRequest = get(
				action.payload,
				'data.GlideContextualSearch_Query.request',
				''
			);
			const searchResults = get(
				action.payload,
				'data.GlideContextualSearch_Query.results',
				[]
			);
			const searchResultsExcludeRecommendation = excludeRecommendation(
				searchResults
			); // Exclude the recommendation from searchResults if any. This should return a copy, instead of a reference.
			const nextActionsData = get(
				action.payload,
				'data.GlideContextualSearch_Query.meta.recorded_actions',
				null
			);
			const returnedResults =
				get(
					action.payload,
					'data.GlideContextualSearch_Query.meta.returned_results',
					0
				) || 0; // This is the number of results returned for current page, including the amount of pinned articles if any.
			const hasMoreResults =
				get(
					action.payload,
					'data.GlideContextualSearch_Query.meta.has_more_results',
					false
				) || false; // True if we can query more results from backend. Note that we may exceed maxlimit.
			const resultsPerPage = get(state, 'configs.limit', 10); // This is the value we configured from Table Configuration.
			const amountOfPinnedArticles = Math.max(
				returnedResults - resultsPerPage,
				0
			); // Get the amount of pinned articles for first page. Only the first page will show pinned articles.

			// Add recommendation item to the search results if it is already in the state
			// coz if it is already in the state the gql query won't fetch it and the searchResults won't have it.
			// We add it here to populate the actionStates object.
			if (state.recommendation.item) {
				searchResults.unshift(state.recommendation.item);
			}

			const actionStates = actionStateUtils.updateActionState(
				nextActionsData,
				{},
				searchResults
			);

			const recommendation = {
				item: state.recommendation.item
					? state.recommendation.item
					: extractRecommendationResult(searchResults)
			};

			const nextPressed = state.detailViewState.nextPageLoading;
			const prevPressed = state.detailViewState.prevPageLoading;
			let detailProps = null;
			// console.log('searchRequest', searchRequest);
			if (state.detailViewState.showDetail) {
				if (nextPressed) {
					detailProps = {
						showDetail: true,
						selectedIndex: searchRequest.meta.window.start,
						// Pagination in detail view should only traverse the normal results excluding the recommendation
						// because we don't support the page navigation in the detail view of the recommendation.
						selectedItem: searchResultsExcludeRecommendation[0]
					};
				} else if (prevPressed) {
					detailProps = {
						showDetail: true,
						selectedIndex:
							searchRequest.meta.window.end +
							amountOfPinnedArticles -
							1,
						// Pagination in detail view should only traverse the normal results excluding the recommendation
						// because we don't support the page navigation in the detail view of the recommendation.
						selectedItem:
							searchResultsExcludeRecommendation[
								searchResultsExcludeRecommendation.length - 1
							]
					};
				}
			}
			updateState({
				configs: {
					...state.configs,
					loading: false
				},
				searchState: {
					...state.searchState,
					isSearching: false,
					searchRequest: searchRequest,
					searchResults: searchResultsExcludeRecommendation,
					returnedResults: returnedResults,
					hasMoreResults: hasMoreResults,
					offset: searchRequest.meta.window.start,
					amountOfPinnedArticles: amountOfPinnedArticles
				},
				recommendation: {
					...state.recommendation,
					...recommendation
				},
				actionStates: actionStates,
				detailViewState: {
					...detailProps
				}
			});
		},
		[AA_SEARCH_FAILED]: coeffects => {
			const { state, action, updateState } = coeffects;
			LOG.error(action.payload.stack);
			updateState({
				...state,
				searchState: {
					...state.searchState,
					error: action.payload
				}
			});
		},
		[AA_UPDATE_SEARCH_STATE_AND_ACTION_STATE]: coeffects => {
			const { state, action, updateState } = coeffects;
			const {
				payload: { searchState, actionStates }
			} = action;
			updateState({
				...state,
				searchState: {
					...state.searchState,
					...searchState
				},
				actionStates: {
					...actionStates
				}
			});
		}
	}
};

