import '../now-agent-assist';
import { t } from 'sn-translate';
import '@servicenow/now-empty-state';

const view = state => {
	const { properties } = state;
	if (!properties.cxsTableConfig) {
		// Covers "", null and undefined
		return (
			<now-empty-state
				type="not-configured"
				headerText={t('No Table Configuration')}
				bodyText={t('Please specify the Table Configuration')}
			/>
		);
	}
	return <now-agent-assist {...properties} is-uib={true}></now-agent-assist>;
};

export default view;
