import {
	AA_TOGGLE_DETAIL_VIEW,
	AA_LIST_ITEM_REFOCUS
} from '../../sn-aa-details-container/actions';
import {
	baseLinkFromRootDir,
	selectResultItemByIndex
} from '../../util/agentAssistUtils';
import { AA_UPDATE_REL_DOC } from '../../sn-aa-action-modal';

const resultItem = (index, recommendation, searchState) => {
	if (index < 0) {
		return recommendation.item;
	} // Recommendation record

	var virtualIndex = index;
	if (searchState.offset) {
		virtualIndex = virtualIndex - searchState.offset;
	}

	if (virtualIndex < 0 || virtualIndex >= searchState.returnedResults) {
		return null;
	}

	return searchState.searchResults[virtualIndex];
};

const toggleViewEffect = ({ state, action, dispatch, updateState }) => {
	const index = action.payload.itemIndex;
	if (action.payload.showDetail === true) {
		const item = resultItem(index, state.recommendation, state.searchState);
		const linkToOpen = baseLinkFromRootDir(item.link);
		if (item && item.linkTarget === '_blank' && linkToOpen) {
			window.open(linkToOpen, '_blank');
		} else {
			updateState({
				detailViewState: {
					showDetail: true,
					selectedItem: item,
					selectedIndex: index
				}
			});
		}
		dispatch(AA_UPDATE_REL_DOC, {
			itemData: item,
			itemIndex: index,
			actionValue: 'Preview',
			remove: false
		});
	} else {
		updateState({
			...state,
			detailViewState: {
				...state.detailViewState,
				showDetail: false,
				selectedItem: null,
				selectedIndex: index
			}
		});
		setTimeout(() => {
			dispatch(AA_LIST_ITEM_REFOCUS, { index });
		}, 300);
	}
};

export const toggleViewActionHandlers = {
	[AA_TOGGLE_DETAIL_VIEW]: toggleViewEffect,
	[AA_LIST_ITEM_REFOCUS]: ({ host, action: { payload } }) => {
		const { index } = payload;
		const article = selectResultItemByIndex(host, index) || host;
		article.focus();
	}
};
