import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import { actionHandlers } from './actionHandlers';
import { LIST_VIEW } from '../consts';

export default createCustomElement('sn-aa-action-header', {
	renderer: {
		type: snabbdom
	},
	view,
	styles,
	properties: {
		view: {default: LIST_VIEW},
		formRecordInfo: {default: {}},
		item: {default: {}},
		itemIndex: {default: 0},
		actions: {default: []},
		visibleActions: {default: new Map()},
		isNewRecord: {default: false},
		isRecommendation: {default: false},
		formTimeStamp: {default : Date.now()}
	},
	initialState: {
		dropdownOpened: false,
		actionsFiltered: false,
		filteredActions: new Set()
	},
	actionHandlers: {
		...actionHandlers
	}
});