import { createHttpEffect } from '@servicenow/ui-effect-http';
import {
	parseId,
	isListView
	// isAdditionalResource
} from '../util/agentAssistUtils';
import {
	COMPONENT_BOOTSTRAPPED,
	COMPONENT_PROPERTY_CHANGED,
	AA_GET_ACTIONS_BY_CONDITION,
	AA_GET_ACTIONS_BY_CONDITION_SUCCEEDED,
	AA_UPDATE_ACTION_HEADER_STATE,
	AA_ACTION_CLICKED
} from './actions';

function getActionsByCondition(coeffects) {
	const {
		state: {
			properties: { item, formRecordInfo, actions }
		},
		dispatch
	} = coeffects;

	const { table: resourceTable, sys_id: resourceId } = parseId(item.id);
	dispatch(AA_GET_ACTIONS_BY_CONDITION, {
		requestData: {
			...formRecordInfo,
			resourceTable: resourceTable,
			resourceId: resourceId,
			allActions: actions
		}
	});
	return true;
}

function dropdownItemClickEffect(coeffects) {
	const { state, dispatch } = coeffects;
	const {
		item: { action, selected }
	} = coeffects.action.payload;
	dispatch(AA_ACTION_CLICKED, {
		action,
		selected,
		item: state.properties.item,
		itemIndex: state.properties.itemIndex
	});
}

function dropdownBtnClickEffect(coeffects) {
	const { state, dispatch } = coeffects;
	if (
		!isListView(state.properties.view) ||
		state.actionsFiltered ||
		state.dropdownOpened ||
		!getActionsByCondition(coeffects)
	) {
		dispatch(AA_UPDATE_ACTION_HEADER_STATE, {
			dropdownOpened: !state.dropdownOpened
		});
	}
}

function getActionsByConditionSucceededEffect(coeffects) {
	const { action, state, dispatch } = coeffects;
	const newActions = new Set(action.payload.result.actions);
	dispatch(AA_UPDATE_ACTION_HEADER_STATE, {
		actionsFiltered: true,
		filteredActions: newActions,
		dropdownOpened: isListView(state.properties.view)
	});
}

function actionClickedHandlerEffect(coeffects) {
	const { state, updateState } = coeffects;
	if (isListView(state.properties.view)) {
		updateState({
			actionsFiltered: false
		});
	}
}

function detailViewBootstrappedEffect(coeffects) {
	const { state } = coeffects;
	if (!isListView(state.properties.view)) {
		getActionsByCondition(coeffects);
	}
}

// DEF0104879: This function will be triggered after formTimeStamp props get changed.
// If current view is list view, then we should reset "actionsFiltered" to be false when form fields (formTimeStamp) just get updated.
// If current view is detail view, then we directly call "AA_GET_ACTIONS_BY_CONDITION" rest api to get latest UI actions after form fields (formTimeStamp) get updated.
function cardAndDetailViewPropsChangedEffect(coeffects) {
	const {
		state,
		action: {
			payload: { name, previousValue, value }
		}
	} = coeffects;
	if (
		!isListView(state.properties.view) &&
		((name === 'item' && previousValue.id !== value.id) ||
			(name === 'formTimeStamp' && previousValue != value))
	) {
		getActionsByCondition(coeffects);
	}
	if (
		isListView(state.properties.view) &&
		(name === 'formTimeStamp' && previousValue != value)
	) {
		actionClickedHandlerEffect(coeffects);
	}
}

const makeDropdownItemClickHandler = () => ({
	effect: dropdownItemClickEffect,
	stopPropagation: true
});

const makeDropdownBtnClickHandler = () => ({
	effect: dropdownBtnClickEffect,
	stopPropagation: true
});

const makeGetActionsByConditionHandler = () => ({
	...createHttpEffect('/api/now/cxs/get_actions_by_condition', {
		method: 'POST',
		dataParam: 'requestData',
		successActionType: AA_GET_ACTIONS_BY_CONDITION_SUCCEEDED
	}),
	stopPropagation: false
});

const makeGetActionsByConditionSucceededHandler = () => ({
	effect: getActionsByConditionSucceededEffect,
	stopPropagation: true
});

const makeUpdateActionContainerStateHandler = () => ({
	effect: coeffects => {
		const { state, action, updateState } = coeffects;
		updateState({
			...state,
			...action.payload
		});
	},
	stopPropagation: true
});

const makeActionClickedHandler = () => ({
	effect: actionClickedHandlerEffect,
	stopPropagation: false
});

const makeDetailViewBootstrappedHandler = () => ({
	effect: detailViewBootstrappedEffect,
	stopPropagation: true
});

const makeCardViewAndDetailViewPropsChangedHandler = () => ({
	effect: cardAndDetailViewPropsChangedEffect,
	stopPropagation: true
});

export const actionHandlers = {
	'NOW_DROPDOWN#ITEM_CLICKED': makeDropdownItemClickHandler(),
	'NOW_DROPDOWN#OPENED_SET': makeDropdownBtnClickHandler(),
	[AA_GET_ACTIONS_BY_CONDITION]: makeGetActionsByConditionHandler(),
	[AA_GET_ACTIONS_BY_CONDITION_SUCCEEDED]: makeGetActionsByConditionSucceededHandler(),
	[AA_UPDATE_ACTION_HEADER_STATE]: makeUpdateActionContainerStateHandler(),
	[AA_ACTION_CLICKED]: makeActionClickedHandler(),
	[COMPONENT_BOOTSTRAPPED]: makeDetailViewBootstrappedHandler(),
	[COMPONENT_PROPERTY_CHANGED]: makeCardViewAndDetailViewPropsChangedHandler()
};
