import { t } from 'sn-translate';
import '@servicenow/now-empty-state';

export const view = state => {
	const {
		properties: { noResources, cxsTableConfigActive }
	} = state;
	let emptyStateHeaderText = '';
	let emptyStateBodyText = '';
	let iconType = '';
	if (!cxsTableConfigActive) {
		iconType = 'permission-error';
		emptyStateHeaderText = t('Invalid configuration');
		emptyStateBodyText = t('Table configuration is inactive.');
	} else {
		if (noResources) {
			iconType = 'no-resources';
			emptyStateHeaderText = t('No Resource Found');
			emptyStateBodyText = t(
				'Please configure search resource for this context.'
			);
		} else {
			iconType = 'empty';
			emptyStateHeaderText = t('No Matches Found');
			emptyStateBodyText = t(
				'Try modifying your search text or filter to find what you’re looking for.'
			);
		}
	}

	return (
		<now-empty-state
			type={iconType}
			headerText={emptyStateHeaderText}
			bodyText={emptyStateBodyText}
		/>
	);
};
