import get from 'lodash/get';

import { AA_PROPOSE_RECOMMENDATION } from './actions';
import { AA_ACTION_CLICKED } from '../../../sn-aa-action-header';

export const view = (state, dispatch) => {
	const {
		data,
		itemIndex,
		action,
		// visibleActions,
		isDetection
		// formRecordInfo
	} = state.properties;

	const actionLabel = get(action, 'displayValue', '');
	if(!actionLabel)
		return null;

	const actionId = get(action, 'actionId', '');
	let proposeUIActionSysId = null;
	if ( !isDetection && (!actionId || actionId.indexOf(':') == -1) )
		return null;
	else
		proposeUIActionSysId = actionId.split(':')[1];

	let onClickHandler = null;
	if(!isDetection && actionId.startsWith('propose:'))
		onClickHandler = () => {
			dispatch(AA_PROPOSE_RECOMMENDATION, {proposeUIActionSysId});
		};
	else
		onClickHandler = () => {
			dispatch(AA_ACTION_CLICKED, {
				item: data,
				itemIndex,
				selected: false,
				action
			});
		};

	return <now-button label={actionLabel} size="md" variant="secondary" bare={true}  on-click={onClickHandler} />;
};
