import get from 'lodash/get';
import escape from 'lodash/escape';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { t } from 'sn-translate';
import makeUIActionRestEffect from './makeUIActionRestEffect';
import {
	fieldEditable,
	setFormFields,
	showTabMessage
} from '../../util/agentAssistUtils';
import { AA_UPDATE_REL_DOC } from '../actions';
import '@servicenow/now-alert';
import { RESET_MODAL_STATE } from './uiActionCommonUtils';

export const AA_LINK_TO_ACTION_MODAL_SUBMIT =
	'AGENT_ASSIST#LINK_TO_ACT_MODAL_SUBMIT';
export const AA_LINK_TO_ACTION_MODAL_CANCEL =
	'AGENT_ASSIST#LINK_TO_ACT_MODAL_CANCEL';
export const LINK_TO = 'link_to';
export const LINK = 'link';
const AA_LINK_MODAL_INPUT = 'AGENT_ASSIST#LINK_MODAL_USER_INPUT_COMMENT';
const AA_LINK_TO_ACTION_SUCCESS = 'AGENT_ASSIST#LINK_TO_ACTION_SUCCESS';
const AA_LINK_TO_ACTION_ERROR = 'AGENT_ASSIST#LINK_TO_ACTION_ERROR';
const linkUtils = {
	getInnerComponent(
		linkModalState = {},
		currentFields = {},
		userInputObj = {},
		dispatch
	) {
		const _inputHandler = userInputObj => {
			dispatch(AA_LINK_MODAL_INPUT, userInputObj);
		};

		const { resourceRecord = {} } = linkModalState; //TODO get
		const alert =
			get(currentFields, 'number.displayValue', false) &&
			get(resourceRecord, 'number', null) ? (
				get(linkModalState, 'confirmationMessage', false) ? (
					<now-alert
						expanded="false"
						manageExpanded="false"
						status="info"
						icon-name="circle-info-outline"
						content={t(
							linkModalState.confirmationMessage,
							resourceRecord.number,
							currentFields.number.displayValue
						)}
					></now-alert>
				) : (
					<now-alert
						expanded="false"
						manageExpanded="false"
						status="info"
						icon-name="circle-info-outline"
						content={t(
							'You are linking the current {0} as a child to {1}.',
							currentFields.number.displayValue,
							resourceRecord.number
						)}
					></now-alert>
				)
			) : null;

		//TODO pass in field object, and add field computation here.
		return (
			<Fragment>
				<div className="aa-modal--body">
					{alert}
					{!get(currentFields, 'work_notes', null) ? null : (
						<div className="aa-modal--input">
							<sn-record-input
								type="textarea"
								label={currentFields['work_notes'].label}
								value={get(
									userInputObj,
									'work_notes.value',
									''
								)}
								maxlength="4000"
								onValueChange={({ value }) =>
									_inputHandler({
										work_notes: {
											value,
											displayValue: value
										}
									})
								}
							/>
						</div>
					)}
					{!get(currentFields, 'comments', null) ? null : (
						<div className="aa-modal--input">
							<sn-record-input
								type="textarea"
								label={currentFields['comments'].label}
								value={get(userInputObj, 'comments.value', '')}
								maxlength="4000"
								onValueChange={({ value }) =>
									_inputHandler({
										comments: {
											value,
											displayValue: value
										}
									})
								}
							/>
						</div>
					)}
				</div>
			</Fragment>
		);
	},

	getFooterActions(userInputObj = '', modalState = {}) {
		return [
			{
				variant: 'primary',
				label: modalState.actionDisplayValue,
				clickActionType: AA_LINK_TO_ACTION_MODAL_SUBMIT,
				userInputObj
			},
			{
				variant: 'secondary',
				label: t('Cancel'),
				clickActionType: RESET_MODAL_STATE
			}
		];
	},

	getHeaderLabel(linkModalState = {}) {
		return linkModalState.modalTitle;
	},

	getInitialModalState() {
		return {
			[LINK_TO]: {},
			[LINK]: {}
		};
	},
	actionHandlers: {
		modalInternalActions: {
			[AA_LINK_MODAL_INPUT]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					updateState({
						...state,
						userInputObj: {
							...state.userInputObj,
							...action.payload
						}
					});
				},
				stopPropagation: true
			}
		},
		modalExternalActions: {
			[LINK_TO]: {
				...makeUIActionRestEffect(
					AA_LINK_TO_ACTION_SUCCESS,
					AA_LINK_TO_ACTION_ERROR
				),
				interceptors: [
					{
						before: context => ({
							...context,
							effects: [
								...context.effects,
								{
									//Store itemData uiActionConfig itemIndex for later use
									effect: coeffects => {
										const {
											state,
											action,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												[LINK_TO]: {
													...state.modalState[
														LINK_TO
													],
													itemData:
														action.payload.itemData,
													itemIndex:
														action.payload
															.itemIndex,
													uiActionConfig:
														action.payload
															.uiActionConfig,
													actionDisplayValue:
														action.payload
															.actionDisplayValue
												}
											}
										});
									},
									args: [context.coeffects]
								}
							]
						})
					}
				],
				stopPropagation: true
			},
			[AA_LINK_TO_ACTION_SUCCESS]: {
				effect: () => {},
				args: [],
				interceptors: [
					{
						before: context => {
							const {
								coeffects: {
									action: {
										payload: { result: { linkField } } = {
											result: { linkField: null }
										},
										payload
									},
									// action,
									dispatch,
									state: {
										properties: { fields, table, sysId }
									}
								},
								coeffects
							} = context;
							let newEffect = {};
							if (
								linkField &&
								fields &&
								fieldEditable(linkField, fields)
							) {
								newEffect = {
									effect: coeffects => {
										const {
											state,
											updateState
										} = coeffects;
										updateState({
											...state,
											modalState: {
												...state.modalState,
												active: true,
												activeModal: LINK_TO,
												[LINK_TO]: {
													...state.modalState[
														LINK_TO
													],
													...payload.result
												}
											}
										});
									},
									args: [coeffects]
								};
							} else {
								newEffect = {
									effect: () => {
										const errorMessage = t(
											'Link action is unavailable because {0} field is not present on current record form',
											fields[linkField]
												? fields[linkField].label
												: linkField
										);
										showTabMessage(
											'error',
											errorMessage,
											table,
											sysId,
											dispatch
										);
									},
									args: []
								};
							}
							return {
								...context,
								effects: [...context.effects, newEffect]
							};
						}
					}
				],
				stopPropagation: true
			},
			[AA_LINK_TO_ACTION_ERROR]: { stopPropagation: true },
			[AA_LINK_TO_ACTION_MODAL_SUBMIT]: coeffects => {
				//dispatch to form, update relevant doc, and clear modalState, show message.
				const { state } = coeffects;
				const {
					state: {
						modalState: {
							[LINK_TO]: {
								uiActionConfig,
								itemData,
								itemIndex,
								resourceRecord,
								linkField,
								successMessage
							}
						},
						properties: { table: currentTable, sysId: currentSysId }
					} = {
						modalState: {
							[LINK_TO]: {
								uiActionConfig: {},
								itemData: {},
								resourceRecord: {},
								linkField: '',
								successMessage: ''
							}
						},
						properties: {
							table: '',
							sysId: ''
						}
					},
					action: {
						payload: {
							footerAction: { userInputObj }
						}
					} = {
						payload: {
							footerAction: { userInputObj: {} }
						}
					},
					dispatch
				} = coeffects;
				//send update rel
				dispatch(AA_UPDATE_REL_DOC, {
					itemData,
					itemIndex,
					actionValue: uiActionConfig.value,
					remove: false
				});

				//add informessage
				const tableName = get(
					itemData,
					'meta.table_name',
					'incident'
				).toLowerCase();
				const cardLabel = get(
					itemData,
					'meta.card_label',
					'incident'
				).toLowerCase();
				let infoMessage = t(
					'{0} has been linked to this {1}',
					escape(resourceRecord.number),
					tableName
				);
				if (cardLabel === 'incident' && tableName === cardLabel) {
					infoMessage = t(
						'{0} has been set as the parent of this {1}',
						escape(resourceRecord.number),
						tableName
					);
				}
				if (successMessage) {
					infoMessage = t(
						successMessage,
						escape(resourceRecord.number),
						get(state, 'currentFields.number.displayValue')
					);
				}
				showTabMessage(
					'info',
					infoMessage,
					currentTable,
					currentSysId,
					dispatch
				);

				//add to form field and save
				setFormFields(
					{
						[linkField]: {
							value: resourceRecord.sys_id,
							displayValue: resourceRecord.number
						},
						...userInputObj
					},
					true, // save form
					dispatch
				);

				//reset modalState
				dispatch(RESET_MODAL_STATE);
			},
			[AA_LINK_TO_ACTION_MODAL_CANCEL]: {
				effect: coeffects => {
					const { state, updateState } = coeffects;
					updateState({
						...state,
						modalState: {
							...state.modalState,
							active: false,
							activeModal: '',
							[LINK_TO]: {}
						}
					});
				},

				stopPropagation: true
			}
		}
	},
	link: ({ action, data, itemIndex }, dispatch) => {
		const [table, recordId] = data.id.split(':');
		if (!table || !recordId) {
			return;
		}
		const [linkType] = action.actionId.split(':');

		dispatch(linkType === 'link_to' ? LINK_TO : LINK, {
			itemData: data,
			uiActionConfig: action,
			itemIndex,
			requestData: {
				resourceTable: table,
				resourceId: recordId,
				actionId: action.actionId,
				isAgentAssist: true
			},
			actionPath: action.restAPI,
			actionDisplayValue: action.displayValue || t('Submit')
		});
	}
};

export default linkUtils;
