import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

createCustomElement('sn-aa-search-as', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		config: {
			reflect: true,
			default: null
		},
		isSearcherSelected: {
			reflect: true,
			default: false
		},
		selected: {
			reflect: true,
			default:false
		},
		user: {
			reflect: true,
			default: null
		},
		agentAssistInstanceId: {
			reflect: true,
			default: ''
		}, 
		isLoading: {
			reflect: true,
			default: false
		}
	},
	styles
});
