import { replaceBadSpaces } from '../../util/agentAssistUtils';
import { createHttpEffect } from '@servicenow/ui-effect-http';
import get from 'lodash/get';
import { AA_POST_ACTION_REFRESH } from '../../now-agent-assist';
import { VISIBLE_ACTIONS } from '../../consts';
import { buildBaseSearchRequestPayload } from '../../now-agent-assist/actionHandlers/searchActionHandlers';

import {
	AA_POST_REL_DOC,
	AA_POST_REL_DOC_SUCCEEDED,
	AA_UPDATE_REL_DOC,
	AA_POST_REL_DOC_AH
} from '../actions';

export const updateRelDocUtils = {
	actions: {
		[AA_UPDATE_REL_DOC]: {
			private: true
		},
		[AA_POST_REL_DOC]: {
			private: true
		}
	},
	actionHandlers: {
		[AA_UPDATE_REL_DOC]: coeffects => {
			const { action, state, dispatch } = coeffects;
			const {
				itemData,
				itemIndex,
				actionValue,
				remove,
				actionId
			} = action.payload;
			const {
				properties: { table: currentTable, sysId: currentSysId },
				searchState: { sourceField, formSearchTerm }
			} = state;
			const sourceDoc = currentTable + ':' + currentSysId;
			const sourceDocUrl = currentTable + '.do?sys_id=' + currentSysId;
			const cleanActionValue = replaceBadSpaces(actionValue);
			//TODO cleanActionValue is action_value which is customer configurable.
			if (remove && !VISIBLE_ACTIONS.has(cleanActionValue)) {return;}

			const requestData = {
				index: itemIndex,
				original_search_terms:
					sourceField === 'additional_search'
						? formSearchTerm
						: 'NULL',
				relevant_doc: itemData.id,
				relevant_doc_url: itemData.link,
				relevance: cleanActionValue,
				relevant: !remove,
				score: itemData.meta.score ? itemData.meta.score : 0,
				source_doc: sourceDoc,
				source_doc_url: sourceDocUrl,
				search_resource_config:
					itemData.meta.source === 'scriptresource' &&
					itemData.meta.resource_details
						? itemData.meta.resource_details.split(':')[0]
						: ''
			};

			requestData.session = currentSysId;
			requestData.displayed_on = 'workspace:' + currentTable;
			//TODO not able to read data from legacy data source.
			requestData.search_request = get(
				state,
				'searchState.searchRequest',
				null
			);

			if (state.properties.isNewRecord) {
				requestData.source_doc = '';
				requestData.source_doc_url = '';
			}
			if (
				(actionId === 'attach' && remove) ||
				actionId === 'this_helped'
			) {
				dispatch(AA_POST_REL_DOC_AH, { requestData });
				return;
			}
			dispatch(AA_POST_REL_DOC, { requestData });
		},
		[AA_POST_REL_DOC_AH]: createHttpEffect('/api/now/cxs/action_handler', {
			method: 'POST',
			dataParam: 'requestData',
			successActionType: AA_POST_REL_DOC_SUCCEEDED
		}),
		[AA_POST_REL_DOC]: createHttpEffect('/api/now/cxs/feedback', {
			method: 'POST',
			dataParam: 'requestData',
			successActionType: AA_POST_REL_DOC_SUCCEEDED
		}),
		[AA_POST_REL_DOC_SUCCEEDED]: coeffects => {
			const { state, dispatch } = coeffects;
			//TODO write pure function to get search results from state
			let fullSearchResults = Array.from(state.searchState.searchResults);
			if (state.recommendation.item)
				{fullSearchResults.unshift(state.recommendation.item);}

			if (!fullSearchResults) {return;}

			let itemsCondition = '';
			fullSearchResults.map(x => {
				itemsCondition += x.id.split(':')[1] + ',';
			});
			// cachebusting
			const now = new Date();
			const nowInMillis = now.getTime();
			const hexString = nowInMillis.toString(16);
			const instanceId = get(state.properties, 'sysId', '') || '';

			itemsCondition = itemsCondition + hexString;
			const condition = `cxs_relevant_doc.sys_user=javascript:gs.getUserID()^cxs_relevant_doc.displayed_onSTARTSWITHworkspace^ORrelevance=This helped^NQrelevance=attach^EQ^cxs_relevant_doc.source_doc=${instanceId}^relevant_docIN${itemsCondition}`;

			const payload = {
				actionStateCondition: condition,
				...buildBaseSearchRequestPayload(state, {
					recommendationOnly: true,
					includeRecommendation: true
				})
			};

			if (itemsCondition) {dispatch(AA_POST_ACTION_REFRESH, payload);}
		}
	}
};
