import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import { actionHandlers } from './actionHandlers';
import styles from './styles.scss';

createCustomElement('sn-aa-details-container', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {
		detailsContainerRef: ''
	},
	properties: {
		parentId: { default: '' },
		formRecordInfo: { default: '' },
		item: { default: '' },
		itemIndex: { default: '' },
		offset: { default: '' },
		actionStates: { default: '' },
		formTimeStamp: { default: Date.now() },
		isRecommendation: { default: false },
		isAdditionalResource: { default: false },
		actions: { default: '' },
		isNewRecord: { default: false },
		allowResultPreviewButtons: { default: true },
		nextItem: { default: '' },
		prevItem: { default: '' },
		showPrev: { default: false },
		showNext: { default: false },
		isUib: {
			reflect: true,
			default: false
		}
	},
	actionHandlers,
	styles: styles
});
