// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import { sanitizeHTMLSnippet } from '../../../util/agentAssistUtils';
import { CARD_VIEW_META_FIELD_LIMIT_PER_LINE } from '../../../consts.js';

const titleRow = (isCommunity, data) => {
	const title = isCommunity ? data.title : data.meta.card_title;
	if (!title) {return null;}
	return (
		<div className="aa-card-body-title">
			<span title={title}>{title}</span>
		</div>
	);
};

const snippetRow = data => {
	const snippet = data.meta.card_snippet;
	if (!snippet) {return null;}

	return (
		<div className="aa-card-body-snippet">
			<span>{sanitizeHTMLSnippet(snippet)}</span>
		</div>
	);
};

const metaRows = (isCommunity, bodyAdditionalFields) => {
	if (
		isCommunity ||
		!bodyAdditionalFields ||
		bodyAdditionalFields.length === 0
	)
		{return null;}

	let additionalFieldsArray = bodyAdditionalFields.map(field => ({
		label: field.fieldLabel,
		value: { type: 'string', value: field.fieldDisplayValue }
	}));

	let metaFieldsNestedArray = [];
	for (
		let i = 0;
		i < additionalFieldsArray.length;
		i += CARD_VIEW_META_FIELD_LIMIT_PER_LINE
	) {
		let items = [];
		for (let j = 0; j < CARD_VIEW_META_FIELD_LIMIT_PER_LINE; j++) {
			if (i + j < additionalFieldsArray.length)
				{items.push(additionalFieldsArray[i + j]);}
			else if (i > 0)
				{items.push({
					label: '',
					value: {
						type: 'string',
						value: ''
					}
				});}
		}
		metaFieldsNestedArray.push(items);
	}

	return (
		<Fragment>
			{metaFieldsNestedArray.map((items, idx) => (
				<div className="aa-card-body-meta-row" key={idx}>
					<now-label-value-stacked
						items={items}
						size="sm"
						truncated
						align="horizontal-equal"
					/>
				</div>
			))}
		</Fragment>
	);
};

export const view = state => {
	let { isCommunity, data, bodyAdditionalFields } = state.properties;
	return (
		<Fragment>
			{titleRow(isCommunity, data)}
			{snippetRow(data)}
			{metaRows(isCommunity, bodyAdditionalFields)}
		</Fragment>
	);
};
