import get from 'lodash/get';
import pick from 'lodash/pick';
import isEmpty from 'lodash/isEmpty';
import isObject from 'lodash/isObject';
import cloneDeep from 'lodash/cloneDeep';
import ConfigGraphQLQuery from './configGraphqlQuery';
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import {
	SEARCHER,
	DEFAULT_CONFIG,
	DEFAULT_RESOURCES_DISPLAY,
	AGENT_ASSIST
} from '../../consts';
import { LOG, showTabMessage } from '../../util/agentAssistUtils';
import { t } from 'sn-translate';

import {
	AA_BOOTSTRAPPED,
	AA_REQUEST_CONFIG,
	AA_CONFIG_REQUEST_START,
	AA_CONFIG_REQUEST_SUCCESS,
	AA_CONFIG_REQUEST_FAILED,
	AA_CONFIG_IN_STATE
} from '../actions';

const getSearcherResourceNames = resources => {
	// Retrieves search resource names from props for rendering in the filter
	if (!resources) {
		return [];
	}

	return Object.keys(resources).map(src => ({
		value: src,
		displayValue: resources[src].displayValue || resources[src].name || src
	}));
};

const sortActions = resourceActions => {
	for (const source in resourceActions) {
		if (resourceActions[source].items) {
			//Sort the actions in each category by order
			resourceActions[source].items.sort((actionA, actionB) => {
				if (actionA.order && actionB.order) {
					if (actionA.order < actionB.order) {
						return -1;
					} else if (actionA.order > actionB.order) {
						return 1;
					}
				} else {
					return 0;
				}
			});
		}
	}
	return resourceActions;
};

const retrieveActions = (cxsResources, isAdditionalResource) => {
	let result = {};
	cxsResources.forEach(resource => {
		const path = isAdditionalResource ? 'cxs_search_res_config.' : '';
		const key = get(resource, path + 'sys_id', null);
		const name = get(resource, path + 'name', key);
		const displayValue = get(resource, path + 'label', name);
		let items = get(resource, path + 'cxs_ui_action_config', []);
		if (typeof items === 'object' && items.length > 0) {
			items = items
				.filter(item => get(item, 'cxs_ui_action', false)) //To handle when cxs_ui_action is empty, e.g. "Link Major Incident" on Similarity result
				// .filter(item => filterActionsByShowOnNew(item, this.props.isNewRecord))
				.map(item => {
					//TODO rename 'name' to 'value'
					return {
						actionId: get(item, 'cxs_ui_action.action_id', ''),
						restAPI: get(item, 'rest_api', ''),
						currentTable: get(item, 'current_table', ''),
						currentActionCondition: get(
							item,
							'current_action_condition',
							''
						),
						table: get(item, 'table', ''),
						actionCondition: get(item, 'action_condition', ''),
						value: get(item, 'action_value', ''),
						order: get(item, 'order', 0),
						badge: get(item, 'badge_label', ''),
						displayValue: get(item, 'action_label', ''),
						reverseDisplayValue: get(item, 'action_undo_label', ''),
						showOnNew: get(item, 'show_on_new', 'false') === 'true',
						kbAttachment: get(
							item,
							'attachment_type',
							'embed_link'
						),
						attachNoteField: get(
							item,
							'attach_note_field',
							'work_notes'
						),
						useAttachNoteField: get(
							item,
							'use_custom_field_for_attach',
							false
						),
						sysId: get(item, 'sys_id', '')
					};
				});
		}
		result[key] = {};
		result[key].items = items;
		result[key].displayValue = displayValue;
		result[key].name = name;
	});
	return sortActions(result);
};

const buildResourceSelectorConfig = cxsTableConfig => {
	const searcherResources = get(
		cxsTableConfig,
		'cxs_search_res_config',
		null
	);
	const searcherResActions = retrieveActions(searcherResources, false);
	const additionalResources = get(
		cxsTableConfig,
		'cxs_res_context_config',
		null
	);
	const additionalResActions = retrieveActions(additionalResources, true);

	let defaultResource = SEARCHER;
	const additionalResNames = additionalResources.map((x, idx) => {
		const resourceSysId = get(
			additionalResources[idx],
			'cxs_search_res_config.sys_id',
			null
		);
		const name = get(
			additionalResources[idx],
			'cxs_search_res_config.name',
			resourceSysId
		);
		const resourceLabel = get(
			additionalResources[idx],
			'cxs_search_res_config.label',
			name
		);
		// const resourceType = get(additionalResources[idx], '_query.addSearchResConfig.cxs_search_res_config._results[0].resource_type', null);

		// Set default search, default_search > searcher > first by order.
		if (get(additionalResources[idx], 'default_search', false)) {
			defaultResource = resourceSysId;
		}

		if (
			defaultResource === SEARCHER &&
			(!searcherResources || searcherResources.length === 0)
		) {
			defaultResource = resourceSysId;
		}

		return {
			value: resourceSysId,
			displayValue: resourceLabel
		};
	});

	return {
		showSearcherFirst: get(cxsTableConfig, 'show_searcher_first', false),
		searcherResources,
		searcherResourceNames: getSearcherResourceNames(searcherResActions),
		searcherResActions,
		defaultResource,
		additionalResources,
		additionalResNames, //Array of {value, displayValue}used for resource selector
		additionalResActions, //{resource sysid: {displayValue, items: [array of actions]}}
		searcherLabel:
			get(
				cxsTableConfig,
				'cxs_context_config.searcher_text',
				DEFAULT_RESOURCES_DISPLAY
			) || DEFAULT_RESOURCES_DISPLAY
	};
};

const buildSearchAsConfig = cxsTableConfig => {
	const searchAsConfigActive = get(cxsTableConfig, 'search_as_active', false);
	const searchAsConfigField = get(cxsTableConfig, 'search_as_field', null);
	//TODO_CONFIG searchAsField display value not available
	const searchAsConfigFieldDisplay = get(
		cxsTableConfig,
		'search_as_field.displayValue',
		null
	);
	let searchAsConfig = {
		active: searchAsConfigActive,
		// disabled: this.filter.defaultResource !== SEARCHER,//need config
		field: searchAsConfigField,
		fieldLabel: searchAsConfigFieldDisplay
	};

	return {
		...searchAsConfig
	};
};

const initializeTableConfig = cxsTableConfig => {
	const configs = {
		// When Configuration is active, then we set "loading" to true to show "Loading Search Results..." before we do the search
		// When Configuration is inactive, then "loading" will be set to false to show "Invalid configuration".
		loading: cxsTableConfig.active,
		error: null,
		active: cxsTableConfig.active,
		limit: Number(
			get(cxsTableConfig, 'results_per_page', DEFAULT_CONFIG.limit)
		),
		context: cxsTableConfig.cxs_context_config_id,
		maxLimit: Number(get(cxsTableConfig, 'limit', DEFAULT_CONFIG.maxLimit)),
		searchFields: cxsTableConfig.cxs_table_field_config,
		allowResultPreviewButtons: get(
			cxsTableConfig,
			'allow_result_preview_buttons',
			true
		),
		//Resource selector related
		...buildResourceSelectorConfig(cxsTableConfig),
		searchAs: buildSearchAsConfig(cxsTableConfig),
		resultsHeaderTextDisplay: get(
			cxsTableConfig,
			'results_header_text',
			AGENT_ASSIST.displayValue
		),
		allowEmptySearchTerm: get(
			cxsTableConfig,
			'allow_empty_search_term',
			false
		)
	};
	return configs;
	// Cache config object
	// ComponentWorkspaceAgentAssist.sharedConfigMap[this.props.properties.cxsTableConfig] = this.config;
};

const buildAndValidateConfigs = data => {
	const cxsTableConfig = get(
		data,
		'GlideContextualSearchMetadata_Query',
		null
	);
	if (!data || !cxsTableConfig || isEmpty(cxsTableConfig)) {
		LOG.warn('No Table Configuration Found. ');
		return {
			error: {
				message: 'No Table Configuration Found. ',
				data: data
			}
		};
	}
	return initializeTableConfig(cxsTableConfig);
};

const initializeSearchAsState = (state, configs) => {
	const { searchAs, defaultResource } = configs;
	const fields = get(state, 'properties.fields', {});
	let user = {};
	if (fields && searchAs && searchAs.field in fields) {
		user = pick(fields[searchAs.field], ['value', 'displayValue', 'label']);
	}

	return {
		...state.searchAsState,
		user,
		isSearcherSelected: defaultResource === SEARCHER
		//On initial search, since single searcher resource cannot be defaulted, only checking searcher grouping here.
	};
};

const getInitialSearchFieldState = (configs, state) => {
	const { fields } = state.properties;
	if (
		!(configs && configs.active) ||
		isEmpty(state) ||
		isEmpty(fields) ||
		!isObject(fields) ||
		!(configs.searchFields && configs.searchFields.length > 0)
	) {
		return {
			searchBoxText: '',
			currentSearchTerm: '',
			formSearchField: null,
			formSearchTerm: '',
			sourceField: null
		};
	}

	let initialSearchField = null;
	//There are fields configured
	const availableSearchFields = configs.searchFields.filter(f => {
		return f.field && f.field in fields;
	});
	if (availableSearchFields.length > 0) {
		//Configured fields are available on form
		const defaultFields = availableSearchFields.filter(
			f => f.default_config
		);
		//ideally there should be just one default field
		if (defaultFields.length > 0) {
			initialSearchField = defaultFields[0].field;
		} else {
			initialSearchField = availableSearchFields[0].field;
		}
	} else {
		//configured fields are not available on form, give warning message
		const searchFieldsStr =
			configs.searchFields.length > 1
				? configs.searchFields.reduce((a, b) => a.name + ', ' + b.name)
				: configs.searchFields[0].name;

		if (searchFieldsStr) {
			showTabMessage(
				'warning',
				t(
					'Agent Assist: The following fields are configured but cannot be found on the form:\n{0}',
					searchFieldsStr
				),
				state.properties.table,
				state.properties.sysId,
				state.dispatch
			);
		}
	}

	if (initialSearchField && initialSearchField in fields) {
		return {
			searchBoxText: '',
			currentSearchTerm: fields[initialSearchField].value,
			formSearchField: initialSearchField,
			formSearchTerm: fields[initialSearchField].value,
			sourceField: initialSearchField
		};
	} else {
		return {
			searchBoxText: '',
			currentSearchTerm: '',
			formSearchField: null,
			formSearchTerm: '',
			sourceField: null
		};
	}
};

export const configUtils = {
	actions: {
		[AA_BOOTSTRAPPED]: {
			private: true
		},
		[AA_REQUEST_CONFIG]: {
			private: true
		}
	},
	actionHandlers: {
		[AA_BOOTSTRAPPED]: coeffects => {
			const {
				state: {
					properties: { cxsTableConfig: cxsTableConfigSysId }
				},
				dispatch
			} = coeffects;

			if (!cxsTableConfigSysId) {
				LOG.error('Invalid Table Configuration ID. ');
				return;
			}

			dispatch(AA_REQUEST_CONFIG, {
				cxsTableConfigSysId
			});
		},
		[AA_REQUEST_CONFIG]: createGraphQLEffect(ConfigGraphQLQuery, {
			variableList: ['cxsTableConfigSysId'],
			startActionType: AA_CONFIG_REQUEST_START,
			successActionType: AA_CONFIG_REQUEST_SUCCESS,
			errorActionType: AA_CONFIG_REQUEST_FAILED
		}),
		[AA_CONFIG_REQUEST_START]: ({ state, updateState }) => {
			updateState({
				...state,
				configs: {
					...state.configs,
					loading: true
				}
			});
		},
		[AA_CONFIG_REQUEST_SUCCESS]: {
			effect: coeffects => {
				const { state, action, updateState } = coeffects;
				const {
					payload: { data }
				} = action;
				const configs = buildAndValidateConfigs(data);
				const seachAsIsConfigured = get(
					configs,
					'searchAs.active',
					false
				);
				const searchAsState = seachAsIsConfigured
					? initializeSearchAsState(state, configs)
					: {};
				updateState({
					...state,
					currentFields: cloneDeep(state.properties.fields),
					configs,
					searchAsState,
					resourceSelectorState: {
						...state.resourceSelectorState,
						active: true,
						selectedResource: configs.defaultResource,
						resources:
							configs.defaultResource === SEARCHER
								? []
								: [configs.defaultResource]
					},
					searchState: {
						...state.searchState,
						...getInitialSearchFieldState(configs, state)
					}
				});
			},
			//resources.cxs_config.data.GlideRecord_Query.cxs_table_config.cxsTableConfigArr[0].results_header_text.value
			interceptors: [
				{
					after: context => {
						return {
							...context,
							effects: [
								...context.effects,
								{
									effect: ({ dispatch }) => {
										dispatch(AA_CONFIG_IN_STATE);
									},
									args: [context.coeffects]
								}
							]
						};
					}
				}
			]
		},
		[AA_CONFIG_REQUEST_FAILED]: coeffects => {
			const { state, action, updateState } = coeffects;

			// Example of an error action.payload
			// {
			// 	locations: [{line: 3, column: 13}],
			// 	message: 'Syntax Error: Cannot parse the unexpected character "\u2014".',
			// 	extensions: undefined,
			// 	nodes: undefined,
			// 	originalError: undefined,
			// 	path: undefined,
			// 	positions: [135],
			// 	source:{body:'', locationOffset: {}, name: 'GraphQL request'},
			// 	stack: "GraphQLError: Syntax Error: Cannot parse the unexpect"
			// }
			LOG.error(action.payload.stack);
			updateState({
				...state,
				configs: {
					...state.configs,
					error: action.payload
				}
			});
		}
	}
};
