import { createHttpEffect } from '@servicenow/ui-effect-http';

export default (successActionType, errorActionType) => {
	return createHttpEffect(
		'/api/now/cxs_actions/:actionPath',
		{
			method: 'POST',
			dataParam: 'requestData',
			pathParams: ['actionPath'],
			successActionType: successActionType,
			errorActionType: errorActionType
		}
	);
};
