import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import pick from 'lodash/pick';
import isObject from 'lodash/isObject';
import '../searchResultsContainer/sn-aa-no-results';
import '../sn-aa-headerbar';
import '../searchResultsContainer/sn-aa-results-container';
import '../searchResultsContainer/sn-aa-results-footer';
import '../searchControlsContainer/sn-aa-search-controls-container';
import '../sn-aa-details-container';
import { t } from 'sn-translate';
import '../sn-aa-action-modal';
//eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '@servicenow/now-empty-state';

import {
	AA_FORM_SEARCH_FIELD_CHANGED,
	AA_FORM_SEARCH_AS_FIELD_CHANGED
} from './actionHandlers/searchActionHandlers';
import {
	isEmptySearchTermAllowed,
	isRecommendation,
	isUibClassDecorator
} from '../util/agentAssistUtils';

const getListViewContent = (
	formRecordInfo,
	actionProps,
	pageProps,
	state,
	error
) => {
	const {
		searchState,
		recommendation,
		configs,
		behaviors,
		instanceId,
		formTimeStamp,
		properties: { isUib }
	} = state;
	const searchResourceResults = searchState.searchResults;
	const showNoResults =
		(state.searchState.sourceField !== 'additional_search' &&
			!state.searchState.currentSearchTerm &&
			!isEmptySearchTermAllowed(state)) ||
		((!searchResourceResults || searchResourceResults.length === 0) &&
			(!recommendation.show || !recommendation.item));
	const noResources =
		(!configs.searcherResourceNames ||
			configs.searcherResourceNames.length === 0) &&
		(!configs.additionalResNames ||
			configs.additionalResNames.length === 0);

	if (!isEmpty(error)) {
		return (
			<div className="aa-panel-body-container">
				<section className="aa-empty-state" aria-live="polite">
					<article hidden>
						{t(
							'An error occured from Agent Assist, please try again'
						)}
					</article>
				</section>
			</div>
		);
	}

	if (searchState.isSearching || configs.loading) {
		// DEF0120472: When it's the initial search and there is no search term available, then we don't show aria-live in DOM.
		return (
			<div className="aa-panel-body-container">
				{state.searchState.isInitialSearch &&
				!state.searchState.currentSearchTerm ? (
					<section className="aa-empty-state">
						<now-empty-state
							type="loading"
							headerText={t('Loading')}
							bodyText={false}
						/>
					</section>
				) : (
					<section className="aa-empty-state" aria-live="polite">
						<now-empty-state
							type="loading"
							headerText={t('Loading')}
							bodyText={false}
						/>
					</section>
				)}
			</div>
		);
	}

	if (showNoResults) {
		// DEF0120472: When it's the initial search and there is no search term available, then we don't show aria-live in DOM.
		return (
			<div className="aa-panel-body-container">
				{state.searchState.isInitialSearch &&
				!state.searchState.currentSearchTerm ? (
					<section className="aa-empty-state">
						<sn-aa-no-results
							no-resources={noResources}
							cxs-table-config-active={configs.active}
						/>
					</section>
				) : (
					<section className="aa-empty-state">
						<sn-aa-no-results
							aria-live="polite"
							no-resources={noResources}
							cxs-table-config-active={configs.active}
						/>
					</section>
				)}
			</div>
		);
	}

	// FIXME : This should never be the case but it is now.
	if (searchState.offset === undefined) {
		searchState.offset = 0;
	}

	return (
		<Fragment>
			<sn-aa-results-container
				key="search-results-container"
				behaviors={behaviors}
				agentAssistInstanceId={instanceId}
				form-record-info={formRecordInfo}
				data={searchResourceResults}
				{...actionProps}
				offset={searchState.offset}
				recommendation={recommendation}
				formTimeStamp={formTimeStamp}
				is-uib={isUib}
			/>
			{!pageProps.showPrev && !pageProps.showNext ? null : (
				<sn-aa-results-footer
					key="search-results-footer"
					{...pageProps}
					is-uib={isUib}
				/>
			)}
		</Fragment>
	);
};

const view = (state, dispatch) => {
	const {
		properties: { table, sysId, isNewRecord, fields, isUib },
		configs,
		instanceId,
		searchState,
		searchAsState,
		resourceSelectorState,
		actionStates,
		componentId,
		modalState,
		currentFields,
		detailViewState: { selectedIndex, selectedItem, showDetail },
		formTimeStamp
	} = state;

	const configuredSearchFields = configs.searchFields;

	// Dispatch page change search if the value of one of the configured search fields changes on the form.
	if (
		!isEmpty(configuredSearchFields) &&
		!isEmpty(currentFields) &&
		!isEmpty(fields) &&
		isObject(fields)
	) {
		var foundChangedField = false;
		configuredSearchFields.forEach(f => {
			if (!(f.field in fields) || foundChangedField) {
				return;
			}

			const oldValue = currentFields[f.field].displayValue;
			const newValue = fields[f.field].displayValue;
			if (oldValue !== newValue) {
				foundChangedField = true;
				dispatch(AA_FORM_SEARCH_FIELD_CHANGED, {
					formSearchField: f.field,
					formSearchTerm: newValue,
					newFields: fields
				});
			}
		});
	}

	const formRecordInfo = {
		currentTable: table,
		currentId: sysId,
		currentFields: fields
	};
	const formAction = isNewRecord ? 'insert' : 'update';
	// const returnedResults = this.getResultCount(this.props);
	const returnedResults = searchState.returnedResults;
	// const hasMoreResults = this.getHasMoreResults(this.props);
	const hasMoreResults = searchState.hasMoreResults;
	// pagination and carousel
	// offset: this is the index of the first result in current page without connsidering pinned articles.
	const offset = searchState.offset;
	const maxLimit = configs.maxLimit;

	// Get the amount of pinned articles for current page so that we can remove it from returnedResults when calculating pageProps
	const amountOfPinnedArticles = searchState.amountOfPinnedArticles;

	// We should exclude the amount of pinned articles from total number of results returned for current page when calculating page index.
	const pageProps = {
		showPrev: offset > 0,
		showNext:
			hasMoreResults &&
			offset + returnedResults - amountOfPinnedArticles < maxLimit
	};

	/*
		The corner case is that: 'selectedIndex' will consider pinned articles at first page only (because pinned articles are only shown in first page), however, both 'offset' and 'maxLimit' are not considering pinned articles.
	  	'returnedResults' includes pinned articles for first page as well.
	  	So we need to remove the amount of pinned articles from 'selectedIndex' for the first page when comparing with 'maxLimit'.
	*/
	const selectedIndexIgnorePinned = selectedIndex - amountOfPinnedArticles;
	const carouselProps = {
		allowResultPreviewButtons: configs.allowResultPreviewButtons,
		showPrev: selectedIndex > 0,
		showNext:
			selectedIndex !== -1 &&
			(hasMoreResults || selectedIndex < offset + returnedResults - 1) &&
			selectedIndexIgnorePinned < maxLimit - 1
	};

	const agentAssistName = get(configs, 'resultsHeaderTextDisplay', '');

	// If search as get configured, then we should take care of searchAsState
	const configSearchAs = get(configs, 'searchAs', {});
	if (!isEmpty(configSearchAs) && configSearchAs.active) {
		// If search as reference field get changed, we need to update searchAsState
		const searchAsFieldValue = configSearchAs.field;
		const searchAsUserSysId = get(searchAsState, 'user.value', '');
		const newSearchAsUserValue = get(
			fields,
			`${searchAsFieldValue}.value`,
			''
		);
		const newSearchAsUserDisplayalue = get(
			fields,
			`${searchAsFieldValue}.displayValue`,
			''
		);
		if (newSearchAsUserValue !== searchAsUserSysId) {
			dispatch(AA_FORM_SEARCH_AS_FIELD_CHANGED, {
				newUserDisplayValue: newSearchAsUserDisplayalue,
				newUserSysId: newSearchAsUserValue
			});
		}
	}

	const searchAsProps = {
		config: configs.searchAs,
		...searchAsState
	};
	const filter = pick(configs, [
		'additionalResNames',
		'defaultSource',
		'searcherLabel',
		'searcherResourceNames',
		'showSearcherFirst'
	]);
	const searchControlsContainerTag = (
		<sn-aa-search-controls-container
			agentAssistInstanceId={instanceId}
			isLoading={searchState.isSearching || configs.loading}
			filter={filter}
			searchAs={searchAsProps}
			action={formAction}
			sysId={sysId}
			tableName={table}
			{...pageProps}
			{...searchState}
			resourceSelectorState={resourceSelectorState}
			is-uib={isUib}
		/>
	);
	const actions = {
		...configs.searcherResActions,
		...configs.additionalResActions
	};

	const actionProps = {
		actions: actions,
		actionStates: actionStates,
		isNewRecord: isNewRecord || false
	};
	const innerContent = !showDetail ? (
		<Fragment>
			<sn-aa-headerbar header-text={agentAssistName} is-uib={isUib} />
			<div className="aa-panel-body-container">
				{searchControlsContainerTag}
				{getListViewContent(
					formRecordInfo,
					actionProps,
					pageProps,
					state,
					configs.error || searchState.error || null
				)}
			</div>
		</Fragment>
	) : (
		<sn-aa-details-container
			parentId={componentId}
			form-record-info={formRecordInfo}
			item={selectedItem}
			itemIndex={selectedIndex}
			offset={offset}
			{...actionProps}
			{...carouselProps}
			form-time-stamp={formTimeStamp}
			isRecommendation={isRecommendation(selectedItem)}
			is-uib={isUib}
		/>
	);
	return (
		<Fragment>
			<div className={isUibClassDecorator(isUib, 'aa-panel')}>
				{innerContent}
			</div>
			{!get(modalState, 'active', false) ? null : (
				<sn-aa-action-modal
					show={get(modalState, 'active', false)}
					actionId={get(modalState, 'activeModal', '')}
					modalState={modalState}
					{...formRecordInfo}
				/>
			)}
		</Fragment>
	);
};

export default view;
