// eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import '../resultListItem/sn-aa-recommendation-record';
import '../resultListItem/sn-aa-result-list-item';
import { RECOMMENDATION_DEFAULT_ID } from '../resultListItem/sn-aa-recommendation-record/view';
import {
	generateUniqueKey,
	getItemActionsMap,
	isUibClassDecorator
} from '../../util/agentAssistUtils';

const recommendationItem = (
	recommendation,
	actionStates,
	formRecordInfo,
	formTimeStamp,
	isUib
) => {
	if (!recommendation || !recommendation.show || !recommendation.item)
		{return null;}
	const recommendationObj = recommendation.item;
	const itemActionStates = actionStates[recommendationObj.id];
	const { source } = recommendationObj.meta;
	const actions = getItemActionsMap(recommendationObj);

	return (
		<sn-aa-recommendation-record
			key={
				recommendationObj.id != RECOMMENDATION_DEFAULT_ID
					? 'recommendation:' + recommendationObj.id
					: generateUniqueKey('recommendation:')
			}
			form-record-info={formRecordInfo}
			data={recommendationObj}
			source={source}
			item-index={-1}
			actions={actions}
			action-states={itemActionStates}
			is-new-record={false}
			recommendation={recommendation}
			formTimeStamp={formTimeStamp}
			is-uib={isUib}
		/>
	);
};

const searchResultList = (
	data,
	behaviors,
	formRecordInfo,
	offset,
	actionStates,
	actions,
	isNewRecord,
	formTimeStamp,
	isUib
) => {
	// Results/data is undefined when we're showing just the recommendation result
	if (data) {
		const fullList = data.map((item, idx) => {
			idx = idx + offset;
			const itemActionStates = actionStates[item.id];
			const { source } = item.meta;
			const itemActions = getItemActionsMap(item, actions).headerActions;

			return (
				<sn-aa-result-list-item
					behaviors={behaviors}
					key={item.id}
					data={item}
					source={source}
					form-record-info={formRecordInfo}
					item-index={idx}
					actions={itemActions}
					action-states={itemActionStates}
					is-new-record={isNewRecord}
					formTimeStamp={formTimeStamp}
				/>
			);
		});

		const fillGrid = () => {
			const totalItems = data.length;
			if (totalItems === 0) {
				return null;
			}
			const baseGrid = 12;
			const maxGrid = 6;
			const idealGridSize = Math.ceil(totalItems / baseGrid) * baseGrid;

			return Array((idealGridSize % totalItems) + maxGrid).fill(
				<div className="aa-filler">.</div>
			);
		};

		return (
			<section
				className={isUibClassDecorator(isUib, 'aa-card-list')}
				aria-live="polite"
			>
				{fullList}
				{fillGrid()}
			</section>
		);
	}
};

export const view = state => {
	const {
		data,
		behaviors,
		formRecordInfo,
		isNewRecord,
		actions,
		offset,
		actionStates,
		recommendation,
		formTimeStamp,
		isUib
	} = state.properties;

	return (
		<Fragment>
			{recommendationItem(
				recommendation,
				actionStates,
				formRecordInfo,
				formTimeStamp,
				isUib
			)}
			{searchResultList(
				data,
				behaviors,
				formRecordInfo,
				offset,
				actionStates,
				actions,
				isNewRecord,
				formTimeStamp,
				isUib
			)}
		</Fragment>
	);
};
