import { searchVariables, searchQuery } from './searchGraphqlQuery';
const actionStateQuery = `
GlideRecord_Query {
	actions: cxs_rel_doc_detail(queryConditions: $actionStateCondition) {
		_results {
			relevance {
				value
			}
			search_term {
				value
			}
			relevant_doc_table {
				value
			}
			relevant_doc {
				value
			}
		}
	}
}`;

const actionStateVaribles = `$actionStateCondition: String!`;
const postActionRefreshGraphqlQuery = `query(
	${actionStateVaribles},
	${searchVariables}
 ) {
	${actionStateQuery}
	${searchQuery}
}`;

export default postActionRefreshGraphqlQuery;
