import isNil from 'lodash/isNil';

import { createElement } from '@servicenow/ui-renderer-snabbdom';
import {
	baseLinkFromRootDir,
	isUibClassDecorator
} from '../util/agentAssistUtils';
import { AA_LOAD_CATALOG_DETAIL_REQUEST } from './actions';
import { t } from 'sn-translate';

const view = (state, dispatch) => {
	const {
		properties: { sysId, table, isUib },
		catalogItem,
		isLoading,
		currentSysId
	} = state;

	if (sysId !== currentSysId && !isLoading) {
		if (table === 'sc_cat_item') {
			dispatch(AA_LOAD_CATALOG_DETAIL_REQUEST, { sys_id: sysId });
		}
	}

	if (isLoading || isNil(catalogItem)) {return null;}

	return (
		<div className={isUibClassDecorator(isUib, 'aa-card')}>
			<div className="aa-card-title">
				<span>{catalogItem.name.displayValue}</span>
			</div>
			{catalogItem.ignore_price.value ||
			!catalogItem.price.value ? null : (
				<div className="aa-card-meta-data">
					<span>{catalogItem.price.displayValue}</span>
				</div>
			)}
			<div className="aa-card-body">
				{!catalogItem.description.value ? null : (
					<div aria-hidden="true">{t('Item Description')}</div>
				)}
				{!catalogItem.picture.displayValue ? null : (
					<div className="aa-card-picture">
						<img
							src={baseLinkFromRootDir(
								catalogItem.picture.displayValue
							)}
						/>
					</div>
				)}
				{createElement('div', {
					props: { innerHTML: catalogItem.description.displayValue }
				})}
			</div>
		</div>
	);
};

export default view;

