import { t } from 'sn-translate';
import '@servicenow/now-label-value';
import {
	processMetaAdditionalFields,
	isUibClassDecorator
} from '../util/agentAssistUtils';
import { DETAIL_VIEW_META_FIELD_LIMIT } from '../consts';
import { createElement } from '@servicenow/ui-renderer-snabbdom';
// import {
// 	AA_RECORD_SET_STATE
// } from './actions';

function getItem(state) {
	return state.properties.item;
}

function getTitle(item) {
	return (
		<div className="aa-card-title" role="heading">
			<span>{item.meta.detail_title}</span>
		</div>
	);
}

function getLink(item, dispatch) {
	const [table, sysId] = item.id.split(':');
	//TODO: add parent table and sys_id
	const previewObj = {
		table: table,
		sys_id: sysId
	};

	return (
		<div className="aa-card-meta-data">
			<now-button
				bare
				label={item.meta.detail_link}
				size="sm"
				variant="secondary"
				on-click={() => {
					dispatch('PREVIEW_RECORD', previewObj);
				}}
			></now-button>
		</div>
	);
}

function getBody(item) {
	return <div className="aa-card-body">{getAdditionalFields(item)}</div>;
}

function getAdditionalFields(item) {
	const additionFieldsObj = processMetaAdditionalFields(
		item.meta.detail_additional_fields,
		DETAIL_VIEW_META_FIELD_LIMIT
	);
	let additionalFieldsArray = [];
	additionFieldsObj.bodyAdditionalFields.map(field => {
		additionalFieldsArray.push({
			label: field.fieldLabel,
			value: { type: 'string', value: field.fieldDisplayValue }
		});
		return null;
	});
	return <now-label-value-stacked items={additionalFieldsArray} size="md" />;
}

function getLatestWorkNote(state) {
	const item = getItem(state);
	if (!item.meta.detail_show_work_note || !state.workNote) {return null;}

	return (
		<div className="aa-card-work-note">
			<div className="aa-card-work-note-label">
				{t('Latest Worknote')}
			</div>
			{createElement('div', {
				props: {
					innerHTML: state.workNote,
					className: 'aa-card-work-note-value'
				}
			})}
		</div>
	);
}

const view = (state, dispatch) => {
	const item = getItem(state);

	return (
		<div className={isUibClassDecorator(state.properties.isUib, 'aa-card')}>
			{getTitle(item)}
			{getLink(item, dispatch)}
			{getBody(item)}
			{getLatestWorkNote(state)}
		</div>
	);
};
export default view;
