import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { default as postActionRefreshGraphqlQuery } from './postActionRefreshGraphqlQuery';
import { searchVariableList } from './searchGraphqlQuery';
import get from 'lodash/get';
import {
	replaceBadSpaces,
	handleSysUiActionEvent
} from '../../util/agentAssistUtils';
import { VISIBLE_ACTIONS } from '../../consts';
import { extractRecommendationResult } from '../../util/agentAssistUtils';
import {
	AA_UPDATE_ACTION_STATE,
	AA_POST_ACTION_REFRESH,
	AA_POST_ACTION_REFRESH_SUCCEEDED,
	AA_UI_ACTION_CLICKED
} from '../actions';

export const actionStateUtils = {
	actions: {
		[AA_UPDATE_ACTION_STATE]: {
			private: true
		},
		[AA_POST_ACTION_REFRESH]: {
			private: true
		},
		[AA_POST_ACTION_REFRESH_SUCCEEDED]: {
			private: true
		},
		[AA_UI_ACTION_CLICKED]: {
			private: true
		}
	},

	actionHandlers: {
		[AA_UPDATE_ACTION_STATE]: coeffects => {
			const { state, action, updateState } = coeffects;
			const {
				payload: { actionStates }
			} = action;
			updateState({
				...state,
				actionStates: {
					...state.actionStates,
					...actionStates
				}
			});
		},
		[AA_POST_ACTION_REFRESH]: createGraphQLEffect(
			postActionRefreshGraphqlQuery,
			{
				variableList: ['actionStateCondition', ...searchVariableList],
				successActionType: AA_POST_ACTION_REFRESH_SUCCEEDED
			}
		),
		[AA_POST_ACTION_REFRESH_SUCCEEDED]: coeffects => {
			const { dispatch, state, action, updateState } = coeffects;
			const recommendationResult = extractRecommendationResult(
				get(
					action,
					'payload.data.GlideContextualSearch_Query.results',
					[]
				)
			);

			updateState({
				path: 'recommendation.item',
				value: recommendationResult,
				operation: 'set'
			});
			//Check if new rec matches the existing, if not, update the existing
			const nextActionsData =
				get(
					action.payload,
					'data.GlideRecord_Query.actions._results',
					[]
				) || [];
			const oldActionStates = state.actionStates;
			const actionStates = actionStateUtils.updateActionState(
				nextActionsData,
				oldActionStates
			);
			dispatch(AA_UPDATE_ACTION_STATE, { actionStates: actionStates });
		},
		[AA_UI_ACTION_CLICKED]: coeffects => {
			const { dispatch, action } = coeffects;
			const actionSysId = action.payload.actionSysId;
			if (actionSysId) {
				handleSysUiActionEvent(actionSysId, dispatch);
			}
		}
	},

	updateActionState: function(nextActionsData, actionStates, searchResults) {
		let newActionState = {};
		// Triggered by search results changes
		if (searchResults) {
			searchResults.forEach(result => {
				newActionState[result.id] = {
					visibleActions: new Map()
				};
			});
		}
		// Triggered by relevant doc changes
		else {
			for (const itemID in actionStates) {
				newActionState[itemID] = {
					visibleActions: new Map()
				};
			}
		}

		if (!nextActionsData) {
			return newActionState;
		}

		nextActionsData.forEach(action => {
			const itemTableName = action.relevant_doc_table
				? action.relevant_doc_table.value || action.relevant_doc_table
				: '';
			const itemSysId = action.relevant_doc
				? action.relevant_doc.value || action.relevant_doc
				: '';
			const actionValue = action.relevance
				? action.relevance.value || action.relevance
				: '';
			const itemID = itemTableName + ':' + itemSysId;

			//TODO: get actionLabel according to configuration later,
			//   note invisible actions might not have label
			const actionLabel = actionValue;
			const actionValueCleanSpaces = replaceBadSpaces(actionValue);

			//TODO action_value is customer configurable.
			//TODO get rid of VISIBLE_ACTIONS and use action badge value instead
			if (
				newActionState[itemID] &&
				VISIBLE_ACTIONS.has(actionValueCleanSpaces) &&
				!newActionState[itemID].visibleActions[actionValueCleanSpaces]
			) {
				const actionProperties = {
					displayValue: actionLabel,
					relevance: action.relevance.value
				};
				newActionState[itemID].visibleActions.set(
					actionValue,
					actionProperties
				);
			}
		});

		return newActionState;
	}
};
