export const searchVariableList = [
	'query',
	'tableConfig',
	'context',
	'searchAs',
	'includeRecommendation',
	'recommendationOnly',
	'includePinnedArticles',
	'resources',
	'limit',
	'offset',
	'maxLimit',
	'formSysID',
	'user',
	'uiType',
	'formTable',
	'sourceField',
	'logSearch',
	'originalQuery',
	'isNewRecord',
	'session',
	'form'
];

export const searchVariables = `$query: String!,
    $tableConfig: String!,
    $context: String!,
    $searchAs: String,
    $includeRecommendation: Boolean,
    $recommendationOnly: Boolean,
    $includePinnedArticles: Boolean,
    $resources: [String],
    $limit: Int,
    $offset: Int,
    $maxLimit: Int,
    $formSysID: String,
    $user: String,
    $uiType: String,
    $formTable: String,
    $sourceField: String,
    $logSearch: Boolean,
    $originalQuery: String,
    $isNewRecord: Boolean,
    $session: String,
    $form: String`;

export const searchQuery = `GlideContextualSearch_Query (
    query: $query,
    tableConfig: $tableConfig,
    context: $context,
    searchAs: $searchAs,
    maxLimit: $maxLimit,
    includeRecommendation: $includeRecommendation,
    recommendationOnly: $recommendationOnly,
    includePinnedArticles: $includePinnedArticles,
    pagination:{limit: $limit, offset: $offset},
    formSysID: $formSysID,
    user: $user,
    resources: $resources,
    uiType: $uiType,
    formTable: $formTable,
    sourceField: $sourceField,
    logSearch: $logSearch,
    originalQuery: $originalQuery,
    isNewRecord: $isNewRecord,
    session: $session,
    form: $form
) {
    request {
        query {
            freetext
        }
        meta {
            window {
                start
                end
            }
        }
        context
        id
    }
    results {
        id,
        title,
        link,
        linkTarget,
        snippet,
        relatedLinks {
            link
            title
            spLink
        }
        ... on GlideContextualSearch_CatalogItemType {
            image {
                link
                thumbnail {
                    link
                }
            }
        }
        meta {
            score
            source
            interleaved
            card_icon
            card_label
            card_title
            additional_fields
            card_snippet
            card_show_footer
            detail_component_name
            detail_title
            detail_link
            detail_additional_fields
            detail_show_work_note
            ... on GlideContextualSearch_CatalogItemMetaType {
                description
            }
            ... on GlideContextualSearch_KnowledgeArticleMetaType {
                author
                link
                number
                published
                modified_display
                viewCount
            }
            ... on GlideContextualSearch_PinnedArticleMetaType {
                pinned
                author
                link
                number
                published
                modified_display
                viewCount
            }
            ... on GlideContextualSearch_SocialQAMetaType {
                author
                published
                modified_display
                viewCount
                answerCount
                votes
            }

            ... on GlideContextualSearch_CommunityBlogMetaType {
                forum
                views
                upvotes
                helpful
                likes
                rating
                modified_display
                full_view_url
                comments
            }

            ... on GlideContextualSearch_CommunityQuestionMetaType {
                forum
                views
                upvotes
                helpful
                likes
                rating
                modified_display
                full_view_url
                comments
            }

            ... on GlideContextualSearch_CommunityAnswerMetaType {
                forum
                views
                upvotes
                helpful
                likes
                rating
                modified_display
                full_view_url
                comments
            }

            ... on GlideContextualSearch_GlideResourceSearchMetaType {
                table_name
                resource_details
                description
            }

            ... on GlideContextualSearch_ScriptResourceSearchMetaType {
                confidence
                resource_details
                table_name
                recommendation_type
                recommendation_title
                recommendation_message
                recommendation_action
                recommendation_result
                recommendation_common_field
            }
        }
    }
    meta {
        has_more_results
        returned_results
        recorded_actions {
          relevance
          search_term
          relevant_doc_table
          relevant_doc
        }
    }
}`;
const SearchGraphQLQuery = `query(
${searchVariables}
) {
    ${searchQuery}
}`;

export default SearchGraphQLQuery;
