import attachUtils from './attachUtils';
import linkUtils from './linkUtils';
import copyResolUtils from './copyResolUtils';
const EDS_MODAL_OPENED_SET = 'NOW_MODAL#OPENED_SET';
export const RESET_MODAL_STATE = 'RESET_MODAL_STATE';

export const getInitialModalState = () => {
	return {
		active: false,
		activeModal: '',
		...attachUtils.getInitialModalState(),
		...linkUtils.getInitialModalState(),
		...copyResolUtils.getInitialModalState()
	};
};

const resetModalState = state => {
	return {
		...state,
		modalState: {
			...getInitialModalState()
		}
	};
};

export const modalActionHandlers = {
	[EDS_MODAL_OPENED_SET]: coeffects => {
		const { state, action, updateState } = coeffects;
		if (!action.payload.value) {
			updateState({ ...resetModalState(state) });
		}
	},
	[RESET_MODAL_STATE]: coeffects => {
		const { state, updateState } = coeffects;

		updateState({ ...resetModalState(state) });
	}
};
