import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

createCustomElement('sn-aa-search-box', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		formSearchTerm: {
			reflect: true,
			default: ''
		},
		searchBoxText: {
			reflect: true,
			default: ''
		},
		isLoading: {
			reflect: true,
			default: false
		}
	},
	styles: styles
});