import { AA_SEARCH_AS_CHANGE_SEARCH } from '../../searchControlsContainer/sn-aa-search-as';
import { AA_RESOURCE_CHANGE_SEARCH } from '../../searchControlsContainer/sn-aa-search-filter';

export const searchControlsContainerUtils = {
	actionHandlers: {
		['NOW_TOGGLE#CHECKED_SET']: {
			effect: coeffects => {
				const { state, action, updateState } = coeffects;
				const {
					payload: { value }
				} = action;
				updateState({
					...state,
					searchAsState: {
						...state.searchAsState,
						selected: value
					}
				});
			},

			stopPropagation: true,
			interceptors: [
				{
					after: context => ({
						...context,
						effects: [
							...context.effects,
							{
								effect: dispatch => {
									const {
										payload: { value: selected }
									} = context.coeffects.action;
									dispatch(AA_SEARCH_AS_CHANGE_SEARCH, {
										searchAsUser: selected
											? context.coeffects.state
													.searchAsState.user.value
											: ''
									});
								},
								args: [context.coeffects.dispatch]
							}
						]
					})
				}
			]
		},

		['NOW_DROPDOWN#SELECTED_ITEMS_SET']: {
			effect: coeffects => {
				const { state, action, updateState } = coeffects;
				const {
					payload: { value }
				} = action;
				updateState({
					...state,
					resourceSelectorState: {
						...state.resourceSelectorState,
						selectedResource: value[0]
					}
				});
			},
			stopPropagation: true,
			interceptors: [
				{
					after: context => ({
						...context,
						effects: [
							...context.effects,
							{
								effect: dispatch => {
									const {
										payload: { value }
									} = context.coeffects.action;
									dispatch(AA_RESOURCE_CHANGE_SEARCH, {
										resourceSysId: value[0]
									});
								},
								args: [context.coeffects.dispatch]
							}
						]
					})
				}
			]
		}
	}
};
