import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './styles.scss';
import { view } from './view';

createCustomElement('sn-aa-results-footer', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		showNext: { default: false },
		showPrev: { default: false },
		children: { default: '' },
		isUib: {
			reflect: true,
			default: false
		}
	},
	styles
});
