import get from 'lodash/get';

import { AA_RESULT_ONCLICK, AA_RESULT_ONKEYPRESS } from './actions';
import {processMetaAdditionalFields} from '../../../util/agentAssistUtils';
import {CARD_VIEW_META_FIELD_LIMIT} from '../../../consts';

import '../sn-aa-article-header';
import '../sn-aa-article-body';
import '../sn-aa-article-footer';

const cardHeader = (data, source, actions, isNewRecord, actionStates, itemIndex, formRecordInfo, formTimeStamp) => {
	return (
		<sn-aa-article-header
			parent-key={data.id}
			key={data.id + '_header'}
			source={source}
			form-record-info={formRecordInfo}
			actions={actions}
			data={data}
			is-new-record={isNewRecord}
			action-states={actionStates}
			item-index={itemIndex}
			is-recommendation={false}
			formTimeStamp={formTimeStamp}
		/>
	);
};

const cardBody = (data, isCommunity, bodyAdditionalFields) => {
	return (
		<sn-aa-article-body
			key={data.id + '_body'}
			is-community={isCommunity}
			data={data}
			body-additional-fields={bodyAdditionalFields}
		/>
	);
};

const cardFooter = (data, isCommunity, source, behaviors, footerAdditionalFields) => {
	const showFooter = get(data, 'meta.card_show_footer', false);
	if (!isCommunity && !showFooter)
		return null;

	return (
		<sn-aa-article-footer
			key={data.id + '_footer'}
			behaviors = {behaviors}
			source-id={data.id}
			source={source}
			meta={data.meta}
			footer-additional-fields={footerAdditionalFields}
		/>
	);
};

const cardOnclick = (event, dispatch, itemIndex, actionName) => {
	// This is to determine whether the click event comes from action dropdown menu button or not
	// If so, we don't open detail view
	if (event.composedPath().some(elem => {
		return elem.tagName === 'DIV' && elem.classList.contains('aa-action-header');
	}))
		return;

	dispatch(actionName, {event : event, showDetail : true, itemIndex : itemIndex});
};

export const view = (state, dispatch) => {
	const {showItem} = state;
	const {data, source, actions, behaviors, 
		isNewRecord, actionStates, itemIndex, 
		// bodyAdditionalFields, footerAdditionalFields, 
		formRecordInfo, formTimeStamp} = state.properties;
	const isCommunity = source === 'community_blog' || source === 'community_question' || source === 'community_answer';
	if (!showItem)
		return null;

	const additionalFieldsObj = processMetaAdditionalFields(data.meta.additional_fields, CARD_VIEW_META_FIELD_LIMIT);
	return (
		<article className="aa-card"
			on-keypress = {(e) => cardOnclick(e, dispatch, itemIndex, AA_RESULT_ONKEYPRESS)}
			on-click = {(e) => cardOnclick(e, dispatch, itemIndex, AA_RESULT_ONCLICK)}
			tabIndex="0"
			role="link">
			{ cardHeader(data,source, actions,isNewRecord, actionStates, itemIndex, formRecordInfo, formTimeStamp) }
			{ cardBody(data, isCommunity, additionalFieldsObj.bodyAdditionalFields) }
			{ cardFooter(data, isCommunity, source, behaviors, additionalFieldsObj.footerAdditionalFields) }
		</article>
	);
};
