import {
	AA_CONTAINER_SET_STATE,
	AA_CONTAINER_SHOW_PREV,
	AA_CONTAINER_SHOW_NEXT
} from './actions';
import { actionTypes } from '@servicenow/ui-core';
import {
	AA_FOOTER_PREV_CLICKED,
	AA_FOOTER_NEXT_CLICKED
} from '../searchResultsContainer/sn-aa-results-footer/actions';
import { autoFocus } from '../util/agentAssistUtils';

const { COMPONENT_DOM_READY } = actionTypes;

const setStateEffect = coeffects => {
	const { state, action, updateState } = coeffects;
	updateState({
		...state,
		...action.payload
	});
};
const showFooterPrevClicked = ({ state, dispatch }) => {
	dispatch(AA_CONTAINER_SHOW_PREV, {
		selectedIndex: state.properties.itemIndex,
		offset: state.properties.offset
	});
};

const showFooterNextClicked = ({ state, dispatch }) => {
	dispatch(AA_CONTAINER_SHOW_NEXT, {
		selectedIndex: state.properties.itemIndex,
		offset: state.properties.offset
	});
};

export const actionHandlers = {
	[COMPONENT_DOM_READY]: ({ host }) => {
		autoFocus(host, '.aa-details-header-content>now-button');
	},
	[AA_CONTAINER_SET_STATE]: {
		effect: setStateEffect,
		stopPropagation: true
	},
	[AA_FOOTER_PREV_CLICKED]: {
		effect: showFooterPrevClicked,
		stopPropagation: true
	},
	[AA_FOOTER_NEXT_CLICKED]: {
		effect: showFooterNextClicked,
		stopPropagation: true
	}
};
