import '../sn-aa-time-ago';
import { t } from 'sn-translate';

const dateField = ( fieldArr, fieldName ) => {
	if (fieldArr && fieldArr.length > 0 && fieldName) {
		const filteredArr = fieldArr.filter(field => field && field.fieldName === fieldName);
		return filteredArr && filteredArr.length > 0 && filteredArr[0];
	}
	return null;
};

function getFooterItems(source, meta, footerAdditionalFields){
	if (source === 'community_blog')
		return [
			{
				'label' : t('Updated'),
				'value' : meta.modified_display,
				'type' : 'timeago',
				'isUTC' : false
			},
			{
				'label' : Number(meta.views) === 1 ? t('view_lowercase'): t('views'),
				'value' : meta.views,
				'type' : 'number'
			},
			{
				'label' : t('Helpful'),
				'value' : meta.helpful,
				'icon' : 'thumbs-up',
				'type' : 'icon-text'
			},
			{
				'label' : Number(meta.comments) === 1 ? t('comment_lowercase'): t('comments_lowercase'),
				'value' : meta.comments,
				'type' : 'number'
			}
		];

	if (source === 'community_question' || source === 'community_answer')
		return [
			{
				'label' : t('Updated'),
				'value' : meta.modified_display,
				'type' : 'timeago',
				'isUTC' : false
			},
			{
				'label' : Number(meta.views) === 1 ? t('view_lowercase'): t('views'),
				'value' : meta.views,
				'type' : 'number'
			},
			{
				'label' : t('Helpful'),
				'value' : meta.helpful,
				'icon' : 'thumbs-up',
				'type' : 'icon-text'
			},
			{
				'label' : Number(meta.comments) === 1 ? t('comment_lowercase'): t('comments_lowercase'),
				'value' : meta.comments,
				'type' : 'number'
			}
		];

	if (source === 'knowledge' || source === 'pinned') {
		let footerItems = [
			{
				'label' : t('Updated'),
				'value' : meta.modified_display,
				'type' : 'timeago',
				'isUTC' : false
			}
		];
		if (source === 'pinned') {
			let pinnedItems = [
				{
					'label' : t('Pinned Article'),
					'value' : '',
					'type' : 'icon-text'
				}
			];
			footerItems = pinnedItems.concat(footerItems);
		} else {
			let notPinnedItems = [
				{
					'label' : Number(meta.viewCount) === 1 ? t('view_lowercase'): t('views'),
					'value' : meta.viewCount,
					'type' : 'number'
				}
			];
			footerItems = footerItems.concat(notPinnedItems);
		}
		return footerItems;
	}

	if (source === 'social') {
		return [
			{
				'label' : t('Last Activity'),
				'value' : meta.modified_display,
				'type' : 'timeago',
				'isUTC' : false
			},
			{
				'label' : Number(meta.viewCount) === 1 ? t('view_lowercase'): t('views'),
				'value' : meta.viewCount,
				'type' : 'number'
			},
			{
				'label' : Number(meta.votes) === 1 ? t('vote'): t('votes'),
				'value' : meta.votes,
				'type' : 'number'
			},
			{
				'label' : Number(meta.answerCount) === 1 ? t('answer_lowercase'): t('answers'),
				'value' : meta.answerCount,
				'type' : 'number'
			}
		];
	}

	if (!footerAdditionalFields || footerAdditionalFields == 0)
		return [];

	let createdOnFields = dateField(footerAdditionalFields, 'sys_created_on');
	let updatedOnFields = dateField(footerAdditionalFields, 'sys_updated_on');

	if (!createdOnFields && !updatedOnFields)
		return [];

	let footerField;
	if (!createdOnFields)
		footerField = updatedOnFields;
	else if (!updatedOnFields)
		footerField = createdOnFields;
	else if (createdOnFields.fieldDisplayValue === updatedOnFields.fieldDisplayValue)
		footerField = createdOnFields;
	else
		footerField = updatedOnFields;
	return [
		{
			'label' : footerField.fieldLabel,
			'value' : footerField.fieldDisplayValue,
			'type' : 'datetime',
			'isUTC' : false
		}
	];
}

export const view = (state) => {
	let {behaviors, meta, source, sourceId, footerAdditionalFields} = state.properties;
	let items = getFooterItems(source, meta, footerAdditionalFields);
	let i = 0;
	return (
		<div className="aa-card-footer">
			{
				items && items.map((item) => {
					i++;
					let seperator = ' · ';
					// DEF0090138: We want a more human-readable, relative time is more valuable for these timestamps in agent assist. With relative time, a quick glance will let someone know if a record is actively being worked on.
					// Text at bottom of card should display human/nice time (e.g. "Created 10 days ago" or "Updated 10 days ago") as seen on Knowledge Article cards.
					// Outage, Problem, Incident, and Change card timestamps should be aligned with the one of knowledge articles.
					if (item.type === 'timeago' || item.type === 'datetime')
						return (
							<span key={sourceId + '_' + i}>{item.label} <sn-aa-time-ago behaviors={behaviors} time_stamp={item.value} system_time={item.isUTC} /> {i < items.length ? seperator : ''}</span>
						);
					else if (item.type === 'number')
						return (
							<span key={sourceId + '_' + i}>{item.value+ ' ' + item.label}{i < items.length ? seperator : ''}</span>
						);
					else if (item.type === 'icon-text')
						return <span key={sourceId + '_' + i}><now-icon icon={item.icon ? item.icon : 'thumbtack-outline'} size='sm'/> {item.label}{(item.value || item.value === 0) ? ' (' + item.value + ')' : ''}{i < items.length ? seperator : ''}</span>;
					else
						return (
							<span key={sourceId + '_' + i}>{item.label + ': ' + item.value}{i < items.length ? seperator : ''}</span>
						);
				})
			}
		</div>
	);
};
