import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './styles.scss';
import {view} from './view';
import {AA_RESULT_ONCLICK, AA_RESULT_ONKEYPRESS} from './actions';
import { AA_TOGGLE_DETAIL_VIEW } from '../../../sn-aa-details-container/actions';

createCustomElement('sn-aa-result-list-item',{
	renderer : {
		type: snabbdom,
		view
	},
	properties : {
		data : {default : ''},
		source : {default : ''},
		actions : {default : ''},
		actionStates : {default : ''},
		behaviors : {default : ''},
		isNewRecord : {default : ''},
		itemIndex : {default : ''},
		formRecordInfo : {default : ''},
		formTimeStamp : {default : Date.now()}
	},
	initialState : {
		showItem : true
	},
	styles,
	actionHandlers : {
		[AA_RESULT_ONCLICK] : {
			effect({action, dispatch}) {
				dispatch(AA_TOGGLE_DETAIL_VIEW, {showDetail : action.payload.showDetail, itemIndex : action.payload.itemIndex});
				action.stopPropagation();
			}
		},
		[AA_RESULT_ONKEYPRESS] : {
			effect({action, dispatch}) {
				if(action.payload.event.key === ' ' || action.payload.event.key === 'Enter')
					dispatch(AA_TOGGLE_DETAIL_VIEW, {showDetail : action.payload.showDetail, itemIndex : action.payload.itemIndex});
				action.stopPropagation();
			}
		}
	}
});