import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import styles from './styles.scss';
import view from './view';
import actionHandlers from './actionHandlers';

createCustomElement('sn-aa-catalog-detail', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		sysId: {
			reflect: true,
			default: ''
		},
		table: {
			reflect: true,
			default: ''
		},
		isUib: {
			reflect: true,
			default: false
		}
	},
	initialState: {
		isLoading: false,
		currentSysId: ''
	},
	actionHandlers,
	styles: styles
});
