import { createElement } from '@servicenow/ui-renderer-snabbdom';

function getTitle(item) {
	return (
		<div className="aa-card-title">
			<span>{item.meta.card_title}</span>
		</div>
	);
}

// Placeholder until form data is available.
function getFormData() {
	return <div className="aa-card-meta-data"></div>;
}

function getBody(item) {
	return (
		<div className="aa-card-body">
			{createElement('div',{ props: { innerHTML: item.meta.card_snippet }})}
		</div>
	);
}

const view = state => {
	const { item } = state.properties;

	return (
		<div className="aa-card">
			{getTitle(item)}
			{getFormData(item)}
			{getBody(item)}
		</div>
	);
};

export default view;
