//eslint-disable-next-line no-unused-vars
import { Fragment } from '@servicenow/ui-renderer-snabbdom';
import isNil from 'lodash/isNil';
import isEmpty from 'lodash/isEmpty';
import { t } from 'sn-translate';
import { LIST_VIEW } from '../../../consts';
import '../../../sn-aa-action-header';

const COMMUNITY_CONFIG = {
	community_blog: { edsIcon: 'quote-left-outline', title: t('Blog') },
	community_question: {
		edsIcon: 'question-circle-outline',
		title: t('Discussion')
	},
	community_answer: {
		edsIcon: 'question-circle-outline',
		title: t('Discussion')
	}
};

const DEFAULT_CARD_ICON = 'document-outline';

const headerIcon = (isRecommendation, source, data) => {
	let cardIcon = isRecommendation
		? 'circle-info-outline'
		: !isNil(data.meta.card_icon)
		? data.meta.card_icon
		: DEFAULT_CARD_ICON;
	if (source in COMMUNITY_CONFIG) {
		cardIcon = COMMUNITY_CONFIG[source].edsIcon;
	}

	if (isEmpty(cardIcon)) {
		cardIcon = DEFAULT_CARD_ICON;
	}

	return <now-icon icon={cardIcon} size="md" />;
};

const getCardLabel = (data, source) => {
	let cardLabel = '';
	if (source in COMMUNITY_CONFIG) {
		cardLabel = COMMUNITY_CONFIG[source].title;
	} else {
		cardLabel = !isNil(data.meta.card_label)
			? data.meta.card_label
			: !isNil(data.meta.table_name)
			? data.meta.table_name
			: t('Record');
	}

	return cardLabel;
};

const headerTitle = (data, parentKey, source) => {
	const cardLabel = getCardLabel(data, source);

	return (
		<span key={parentKey + '_div'} className="aa-card-header-title">
			{cardLabel}
		</span>
	);
};

const header = (isRecommendation, source, parentKey, data) => {
	if (!source) {
		return null;
	}

	return (
		<Fragment>
			{headerIcon(isRecommendation, source, data)}
			{headerTitle(data, parentKey, source)}
		</Fragment>
	);
};

export const view = state => {
	const {
		isRecommendation,
		parentKey,
		formRecordInfo,
		source,
		data,
		itemIndex,
		isNewRecord,
		actions,
		actionStates,
		formTimeStamp
	} = state.properties;

	const cardLabel = getCardLabel(data, source);

	return (
		<h3
			aria-label={cardLabel + ' ' + data.title}
			className={
				'aa-card-header ' +
				(isRecommendation ? 'is-recommendation' : '')
			}
			id={parentKey}
		>
			<div key={parentKey} className="aa-card-header-content">
				{header(isRecommendation, source, parentKey, data)}
			</div>
			<div className="aa-card-header-content">
				<sn-aa-action-header
					className="aa-action"
					view={LIST_VIEW}
					formRecordInfo={formRecordInfo}
					item={data}
					itemIndex={itemIndex}
					source={source}
					actions={actions}
					{...actionStates}
					isNewRecord={isNewRecord}
					isRecommendation={isRecommendation}
					formTimeStamp={formTimeStamp}
				/>
			</div>
		</h3>
	);
};
