import get from 'lodash/get';
import unescape from 'lodash/unescape';
import {
	LIST_VIEW,
	DEFAULT_CONFIG,
	TASK_SOURCE_TYPES,
	GFORM_SAVE,
	GFORM_INTERNAL_BATCH_ACTIONS,
	GFORM_SET_VALUE,
	ADD_NOTIFICATIONS
} from '../consts';
import { UI_ACTION_BAR_ACTIONS } from '@servicenow/now-record-common';
import { createLog } from '../util/logger';

export const LOG = createLog('snComponentWorkspaceAgentAssist');

/**
 * Dispatch NOW_RECORD_UI_ACTION_BAR#CLICKED to submit a System UI Action, such as "Propose Major Incident".
 * It should support the workspace client script for System UI Action.
 *
 * @param {string} actionSysId - The sys_id of the System UI Action.
 * @param {object} dispatch
 */
export function handleSysUiActionEvent(actionSysId, dispatch) {
	dispatch(UI_ACTION_BAR_ACTIONS.ACTION_CLICK, {
		actionSysId: actionSysId
	});
}

export function setFormFields(fields, saveForm, dispatch) {
	if (!fields || !dispatch) {
		return;
	}

	let actions = [];
	const fieldNamesArr = Object.keys(fields);

	for (let i = 0; i < fieldNamesArr.length; i++) {
		const fieldName = fieldNamesArr[i];
		const value = fields[fieldName].value;
		const displayValue = fields[fieldName].displayValue;

		const action = {
			name: GFORM_SET_VALUE,
			payload: { fieldName, value, displayValue }
		};
		actions.push(action);
	}

	if (saveForm) {
		// Append GFORM_SAVE action
		const saveFormAction = {
			name: GFORM_SAVE,
			payload: {}
		};
		actions.push(saveFormAction);
	}

	dispatch(GFORM_INTERNAL_BATCH_ACTIONS, {
		actions: actions
	});
}

export function fieldEditable(fieldName, fields) {
	if (
		!fieldName ||
		!Object.prototype.hasOwnProperty.call(fields, fieldName)
	) {
		return false;
	}

	const { readonly, sys_readonly, visible } = fields[fieldName];
	if (!visible || readonly || sys_readonly) {
		return false;
	}

	//If the field is visible, and not readonly by any means, return true
	return true;
}

export function replaceBadSpaces(string) {
	return decodeURIComponent(
		encodeURIComponent(string)
			.replace(/%C2%A0/g, '%20')
			.replace(/%09%09+|%20%20+/g, '')
	);
}

export function showTabMessage(type, message, table, sysId, dispatch) {
	dispatch(ADD_NOTIFICATIONS, {
		notifications: [
			{
				type: type,
				message: message,
				sys_id: sysId,
				table: table
			}
		]
	});
}

// This can be refactored by using
// condition in ui action config
function filterActionsByResult(actions, item) {
	const { source } = item.meta;
	if (!actions || !item) {
		return null;
	}

	if (source === 'catalog') {
		//if there is no relatedLink for order, we don't show order button.
		if (
			!item.relatedLinks ||
			item.relatedLinks.length === 0 ||
			!item.relatedLinks.some(
				link => link.title.toLowerCase() === 'order'
			)
		) {
			return actions.filter(action => action.actionId !== 'order');
		}
	}

	return actions;
}

export function filterActionsByShowOnNew(actions, isNewRecord) {
	if (!isNewRecord) {
		return actions;
	}

	return actions.filter(action => action.showOnNew);
}

export function getItemActionsMap(item, actions) {
	const { source } = item.meta;
	if (isRecommendation(item)) {
		return getRecommendationActions(item);
	}

	if (!isAdditionalResource(item)) {
		for (const key in actions) {
			if (actions[key].name === source) {
				return {
					footerActions: [],
					headerActions: filterActionsByResult(
						actions[key].items,
						item
					)
				};
			}
		}
	}

	if (isAdditionalResource(item) && item.meta.resource_details) {
		const [resourceId] = item.meta.resource_details.split(':');
		return {
			footerActions: [],
			headerActions: actions[resourceId].items
		};
	}

	return {
		footerActions: [],
		headerActions: []
	};
}

function getRecommendationActions(recommendationObj) {
	let recommendationAction = get(
		recommendationObj,
		'meta.recommendation_action',
		''
	);
	return {
		footerActions: [
			{
				actionId: get(
					recommendationAction,
					'primary_action.actionId',
					''
				),
				actionName: get(
					recommendationAction,
					'primary_action.actionName',
					''
				),
				value: get(recommendationAction, 'primary_action.value', ''),
				restAPI: get(
					recommendationAction,
					'primary_action.restAPI',
					''
				),
				displayValue: get(
					recommendationAction,
					'primary_action.displaValue',
					''
				),
				badge: get(recommendationAction, 'primary_action.badge', ''),
				reverseDisplayValue: get(
					recommendationAction,
					'primary_action.reverseDisplayValue',
					''
				)
			}
		],
		headerActions: [...get(recommendationAction, 'additional_actions', '')]
	};
}

export function processMetaAdditionalFields(additionalFields, limit) {
	let returnObj = {
		bodyAdditionalFields: [],
		footerAdditionalFields: []
	};
	//e.g. 'state///State///Closed|||impact///Impact///3 - Low|||category///Category///Inquiry / Help|||caller_id///Caller///Rick Berzle|||assigned_to///Assigned to///Don Goodliffe'
	if (!additionalFields) {
		return returnObj;
	}

	additionalFields.split('|||').map((field, idx, arr) => {
		// limit field number, excluding footer fields
		if (idx >= limit && idx < arr.length - 2) {
			return null;
		}

		const fieldValues = field.split('///');
		if (
			fieldValues.length < 3 ||
			!fieldValues[2] ||
			fieldValues[2] == 'null'
		) {
			return null;
		}

		const fieldObj = {
			fieldName: fieldValues[0],
			fieldLabel: fieldValues[1],
			fieldDisplayValue: fieldValues[2]
		};
		if (idx < arr.length - 2) {
			returnObj.bodyAdditionalFields.push(fieldObj);
		} else {
			returnObj.footerAdditionalFields.push(fieldObj);
		}
		return null;
	});
	return returnObj;
}

/**
 * util function for figuring out which field to attach to
 * @param {object} attachActionConfig - the action config of attach
 * @param {object} fields - field object of current form
 * @param {string} currentTable - table name of current form record
 * @returns {object} returns the object to which field text will be attached; empty object means, don't attach to any field, only log the action; null means no valid field found.
 */
export function getAttachFieldObj(attachActionConfig, fields, currentTable) {
	let attachFieldName = null;
	let fieldArray = [];
	const { useAttachNoteField, attachNoteField } = attachActionConfig;
	if (
		((typeof useAttachNoteField === 'boolean' && useAttachNoteField) ||
			useAttachNoteField === 'true') &&
		!attachNoteField
	) {
		return {};
	}

	if (
		((typeof useAttachNoteField === 'boolean' && useAttachNoteField) ||
			useAttachNoteField === 'true') &&
		attachNoteField
	) {
		fieldArray = attachNoteField.split(',');
	}

	for (const item of fieldArray.concat(DEFAULT_CONFIG.attachmentFields)) {
		if (!item) {
			break;
		}

		let table = null;
		let field = item;
		if (item.includes('.')) {
			[table, field] = item.split('.');
		}
		if (table && table !== currentTable) {
			continue;
		}

		if (!fieldEditable(field, fields)) {
			continue;
		}

		attachFieldName = field;
		break;
	}

	if (attachFieldName) {
		return {
			...fields[attachFieldName],
			name: attachFieldName
		};
	}

	return null;
}

export function parseId(id) {
	if (!id || !id.length) {
		return null;
	}

	id = id.split(':');
	if (id.length !== 2) {
		return {
			table: '',
			sys_id: ''
		};
	}

	return {
		table: id[0],
		sys_id: id[1]
	};
}

export function isRecommendation(data) {
	const recommendationType = get(data, 'meta.recommendation_type', '');
	return (
		recommendationType === 'detection' || recommendationType === 'proposal'
	);
}

export function extractRecommendationResult(results) {
	if (results.length === 0) {
		return null;
	}

	if (!isRecommendation(results[0])) {
		return null;
	}

	var item = parseRecommendationObj(results[0]);
	return item;
}

function parseRecommendationObj(item) {
	return {
		...item,
		meta: {
			...item.meta,
			recommendation_result: JSON.parse(
				get(item, 'meta.recommendation_result', '')
			),
			recommendation_action: JSON.parse(
				get(item, 'meta.recommendation_action', '')
			),
			recommendation_common_field: JSON.parse(
				get(item, 'meta.recommendation_common_field', '')
			)
		}
	};
}

export function isEmptySearchTermAllowed(state) {
	if (!state.configs.allowEmptySearchTerm) {
		return false;
	}

	const resource = state.resourceSelectorState.selectedResource;
	const addResNames = state.configs.additionalResNames;
	return addResNames.some(e => {
		return e.value === resource;
	});
}

export function isAdditionalResource(data) {
	if (isRecommendation(data)) {
		return false;
	}

	const { source } = data.meta;
	return TASK_SOURCE_TYPES.includes(source);
}

export function isListView(view) {
	return view === LIST_VIEW;
}

export function generateUniqueKey(pre) {
	return `${pre}_${new Date().getTime()}`;
}

export function sanitizeHTMLSnippet(str) {
	const sanitized = str
		.replace(/(<([^>]+)>)/gi, '')
		.replace(/&nbsp;/gi, ' ')
		//substring 500 to reduce the length rendered to dom, improve performance
		.substring(0, 500);
	return unescape(sanitized);
}

export function copyToClipboard(str) {
	return new Promise(function(resolve, reject) {
		const tempElement = document.createElement('div');
		tempElement.innerHTML = str;

		// Hide the temporary element & move it offscreen
		tempElement.style.height = '0';
		tempElement.style.opacity = '0';
		tempElement.style.position = 'fixed';
		tempElement.style.left = '-9999px';
		tempElement.style.zIndex = '-1';

		document.body.appendChild(tempElement);
		window.getSelection().removeAllRanges();

		const range = document.createRange();
		range.selectNode(tempElement);
		window.getSelection().addRange(range);

		let success;
		try {
			success = document.execCommand('copy');
		} catch (err) {
			success = false;
		} finally {
			document.body.removeChild(tempElement);
		}

		return success ? resolve() : reject();
	});
}

export function baseLinkFromRootDir(link) {
	if (!link) {
		return '';
	}

	if (link.startsWith('/') || link.startsWith('http')) {
		return link;
	}

	return '/' + link;
}

export function isEmptyObj(obj) {
	for (let key in obj) {
		if (Object.prototype.hasOwnProperty.call(obj, key)) {
			return false;
		}
	}
	return true;
}

export function autoFocus(host, selector) {
	setTimeout(() => host.shadowRoot.querySelector(selector).focus(), 100);
}

export function isUibClassDecorator(isUib, cssClass) {
	return cssClass + (isUib ? ' is-uib' : '');
}

export function selectResultItemByIndex(nowAgentAssistEl, i) {
	let resultItemEl = null;
	try {
		resultItemEl = nowAgentAssistEl.shadowRoot
			.querySelector('sn-aa-results-container')
			.shadowRoot.querySelector('sn-aa-results-list')
			.shadowRoot.querySelector(
				`sn-aa-result-list-item:nth-child(${i + 1})`
			)
			.shadowRoot.querySelector('article');
	} catch (error) {
		resultItemEl = null;
	}
	return resultItemEl;
}
