import { createCustomElement } from '@servicenow/ui-core';
import { actionHandlers } from './actionHandler';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';
import { AA_GET_LATEST_WORKNOTE } from './actions';

export default createCustomElement('sn-aa-record-detail', {
	renderer: {
		type: snabbdom,
		view
	},

	legacyHandleAllActions: true,

	properties: {
		item: { default: {} },
		isUib: {
			reflect: true,
			default: false
		}
	},

	initialState: {
		workNote: '',
		showLatestWorkNote: ''
	},
	actionHandlers,
	onConnect(host, dispatch) {
		dispatch(AA_GET_LATEST_WORKNOTE, {
			record_id: host.item.id.split(':')[1],
			table_name: host.item.id.split(':')[0]
		});
	},
	styles: styles
});
