import { t } from 'sn-translate';
import '@servicenow/now-toggle';

const view = state => {
	const {
		properties: {
			agentAssistInstanceId,
			isLoading,
			isSearcherSelected,
			user,
			config,
			selected
		}
	} = state;
	const tagIDSuffix = '-searchas-user-list-label';

	if (
		!isSearcherSelected ||
		!user ||
		!user.value ||
		!(config && config.active)
	)
		{return null;}
	const headerText = t('Search as {0}', config.fieldLabel || user.label);

	return (
		<div className="aa-search-as" id={agentAssistInstanceId + tagIDSuffix}>
			<div className="aa-search-as-content">
				<span>{headerText}</span>
			</div>
			<div className="aa-search-as-content">
				<now-toggle size="sm" disabled={isLoading} checked={selected} />
			</div>
		</div>
	);
};

export default view;
