import { createCustomElement } from '@servicenow/ui-core';
import { actionMappers } from './actionMappers';
import snabbdom from '@servicenow/ui-renderer-snabbdom';
import view from './view';
import styles from './styles.scss';

/**
 * Automatically searches for relevant resources when an agent is working on a record in a workspace.
 *
 * ```jsx
 * <sn-agent-assist-uib
 * 	table={form.table}
 * 	sys-id={form.sysId}
 * 	fields={form.fields}
 * 	is-new-record={form.isNewRecord}
 * 	cxs-table-config={cxsTableConfig}
 * />
 * ```
 *
 * @seismicElement sn-agent-assist-uib
 * @summary Provides a list of search results that are relevant to a record.  
 */
export default createCustomElement('sn-agent-assist-uib', {
	renderer: {
		type: snabbdom,
		view
	},
	initialState: {},
	properties: {
		/**
		 * The table name of the record that agent assist appears on.
		 * @type {string}
		 */
		table: { default: '' },
		/**
		 * The Sys ID of the record that agent assist appears on.
		 * @type {string}
		 */
		sysId: { default: '' },
		/**
		 * An object that defines the fields of the record that agent assist appears on.
		 * @type {object}
		 */
		fields: { default: {} },
		/**
		 * When true, the record is new.
		 * @type {boolean}
		 */
		isNewRecord: { default: false },
		/**
		 * The Sys ID of contextual search table configuration.
		 * @type {string}
		 */
		cxsTableConfig: { default: '' }
	},
	actionHandlers: actionMappers,
	styles
});
