import { externalActionMap, GFORM_INTERNAL_BATCH_ACTIONS } from '../consts';
import isArray from 'lodash/isArray';

let actionMappers = {};

Object.keys(externalActionMap).forEach(e => {
	actionMappers[e] = {
		effect: ({ action, dispatch }) => {
			dispatch(externalActionMap[e], action.payload);
		},
		stopPropagation: true
	};
});

actionMappers[GFORM_INTERNAL_BATCH_ACTIONS] = {
	effect: ({
		action: {
			payload: { actions }
		},
		dispatch
	}) => {
		if (isArray(actions)) {
			actions.forEach(e => {
				dispatch(externalActionMap[e.name], e.payload);
			});
		}
	},
	stopPropagation: true
};

export { actionMappers };
