import { createCustomElement } from '@servicenow/ui-core';
import snabbdom from '@servicenow/ui-renderer-snabbdom';

import styles from './styles.scss';
import { view } from './view';

createCustomElement('sn-aa-results-container', {
	renderer: {
		type: snabbdom,
		view
	},
	properties: {
		key: { default: '' },
		behaviors: { default: '' },
		agentAssistInstanceId: { default: '' },
		formRecordInfo: { default: '' },
		data: { default: '' },
		actions: { default: '' },
		actionStates: { default: '' },
		isNewRecord: { default: '' },
		offset: { default: '' },
		recommendation: { default: '' },
		formTimeStamp: { default: Date.now() },
		isUib: {
			reflect: true,
			default: false
		}
	},
	styles
});
