import get from 'lodash/get';
import isEmpty from 'lodash/isEmpty';
import '@servicenow/now-alert';
import { Fragment } from '@servicenow/ui-renderer-snabbdom'; //eslint-disable-line no-unused-vars
import { createGraphQLEffect } from '@servicenow/ui-effect-graphql';
import { t } from 'sn-translate';
import { parseId, showTabMessage, LOG } from '../../util/agentAssistUtils';
import { RESET_MODAL_STATE } from './uiActionCommonUtils';

export const FLAG_ACTION_ID = 'flag'; //to match the action_id in cxs_ui_action
export const FLAG_ACTION_PATH = 'flag'; //to match rest resource name
export const AA_FLAG_ACT_MODAL_SUBMIT = 'AGENT_ASSIST#FLAG_ACT_MODAL#SUBMIT';
export const AA_FLAG_MODAL_USER_INPUT =
	'AGENT_ASSIST#FLAG_ACT_MODAL#USER_INPUT';
export const AA_FLAG_ACTION = 'AGENT_ASSIST#FLAG_ACTION';
export const AA_INSERT_KB_ARTICLE_REQUEST =
	'AGENT_ASSIST#INSERT_KB_ARTICLE_REQUEST';
export const AA_INSERT_KB_ARTICLE_REQUEST_STARTED =
	'AGENT_ASSIST#INSERT_KB_ARTICLE_REQUEST_STARTED';
export const AA_INSERT_KB_ARTICLE_SUCCEEDED =
	'AGENT_ASSIST#INSERT_KB_ARTICLE_SUCCEEDED';
export const AA_INSERT_KB_ARTICLE_FAILED =
	'AGENT_ASSIST#INSERT_KB_ARTICLE_FAILED';

const SAVE_DISPLAY = t('Save');
const CANCEL_DISPLAY = t('Cancel');
const FLAG_ARTICLE_DISPLAY = t('Flag Article');
const FLAG_FIELD = 'comment';

const insertKBFeedbackQuery = `mutation ($comment: String, $article: String, $reason: String){
	GlideRecord_Mutation{
		insert_kb_feedback(comments: $comment, article: $article, flagged: "true", reason: $reason) {
			comments{
				value
			}
			article{
				value
			}
			flagged{
				value
			}
			reason{
				value
			}
		}
	}
}`;

const insertKBFeedbackEffect = createGraphQLEffect(insertKBFeedbackQuery, {
	variableList: ['comment', 'article', 'reason'],
	startActionType: AA_INSERT_KB_ARTICLE_REQUEST_STARTED,
	successActionType: AA_INSERT_KB_ARTICLE_SUCCEEDED,
	errorActionType: AA_INSERT_KB_ARTICLE_FAILED
});

const insertKBFeedbackSucceededEffect = coeffects => {
	const { dispatch, state } = coeffects;
	const {
		properties: { table, sysId }
	} = state;
	//add info message
	showTabMessage(
		'info',
		t('Article has been flagged'),
		table,
		sysId,
		dispatch
	);
	//reset modalState
	dispatch(RESET_MODAL_STATE);
};

const insertKBFeedbackFailedEffect = ({ state, updateState }) => {
	const {
		properties: { table, sysId }
	} = state;
	LOG.error(
		'Failed to flag the article - table = ' + table + ' , sysId = ' + sysId
	);
	updateState({
		modalState: {
			...state.modalState,
			[FLAG_ACTION_ID]: { errorMessage: t('Failed to flag the article') }
		}
	});
};

//TODO multi export vs single export
const flagUtils = {
	getInnerComponent(
		flagModalState = {},
		currentFields = {}, //eslint-disable-line no-unused-vars
		userInputObj = {},
		dispatch
	) {
		//eslint-disable-line no-unused-vars
		const _inputHandler = userInputObj => {
			dispatch(AA_FLAG_MODAL_USER_INPUT, userInputObj);
		};

		const userInputValue = get(userInputObj, `${FLAG_FIELD}.value`, '');
		const errorMessage = get(flagModalState, 'errorMessage', '');
		let errorMessageAlert = [];
		if (!isEmpty(errorMessage)) {
			errorMessageAlert.push(
				<div>
					<now-alert
						content={errorMessage}
						status="critical"
						icon-name="circle-close-outline"
					></now-alert>
				</div>
			);
		}

		return (
			<Fragment>
				{errorMessageAlert}
				<div className="sn-aa-action-confirm-modal--body">
					<sn-record-input
						type="textarea"
						maxlength="4000"
						required={true}
						name="flag-comments"
						label={t('Please provide a comment:')}
						display-value={t('Comments')}
						onValueChange={(
							{ value, displayValue } //eslint-disable-line no-unused-vars
						) =>
							_inputHandler({
								[get(
									flagModalState,
									'modalFieldObj.name',
									FLAG_FIELD
								)]: {
									value
								}
							})
						}
						value={userInputValue}
						autofocus={true}
						invalid={!userInputValue ? true : false}
					/>
				</div>
			</Fragment>
		);
	},

	getFooterActions(userInputObj = {}) {
		const userInputValue = get(userInputObj, `${FLAG_FIELD}.value`, '');
		return [
			{
				variant: 'primary',
				label: SAVE_DISPLAY,
				clickActionType: AA_FLAG_ACT_MODAL_SUBMIT,
				userInputObj,
				disabled: !userInputValue ? true : false
			},
			{
				variant: 'secondary',
				label: CANCEL_DISPLAY,
				clickActionType: RESET_MODAL_STATE
			}
		];
	},

	getHeaderLabel() {
		return FLAG_ARTICLE_DISPLAY;
	},

	getInitialModalState() {
		return {
			[FLAG_ACTION_ID]: {}
		};
	},
	actionHandlers: {
		modalInternalActions: {
			[AA_FLAG_MODAL_USER_INPUT]: {
				effect: coeffects => {
					const { state, action, updateState } = coeffects;
					updateState({
						...state,
						userInputObj: action.payload
					});
				},
				stopPropagation: true
			}
		},
		modalExternalActions: {
			[AA_FLAG_ACTION]: coeffects => {
				const { state, action, updateState } = coeffects;
				updateState({
					...state,
					modalState: {
						...state.modalState,
						active: true,
						activeModal: FLAG_ACTION_ID,
						[FLAG_ACTION_ID]: {
							actionConfirmModalProps: {
								...action.payload.actionConfirmModalProps
							}
						}
					}
				});
			},
			[AA_FLAG_ACT_MODAL_SUBMIT]: {
				effect: coeffects => {
					const {
						state: {
							modalState: {
								[FLAG_ACTION_ID]: { actionConfirmModalProps }
							}
							// properties: {
							// 	table: currentTable,
							// 	sysId: currentSysId
							// }
						} = {
							modalState: {
								[FLAG_ACTION_ID]: {
									actionConfirmModalProps: {}
								}
							},
							properties: {
								table: '',
								sysId: ''
							}
						},
						action: {
							payload: {
								footerAction: { userInputObj }
							}
						} = {
							payload: {
								footerAction: { userInputObj: null }
							}
						},
						dispatch
					} = coeffects;

					const flagComment = get(
						userInputObj,
						`${FLAG_FIELD}.value`,
						''
					);
					if (flagComment.length > 0) {
						const article = parseId(
							actionConfirmModalProps.data.id
						);
						if (article.sys_id) {
							let reason = '';
							if (
								coeffects.state.sysProperties
									.createTaskOnFlag == true
							) {
								reason = 'Flagged';
							}
							dispatch(AA_INSERT_KB_ARTICLE_REQUEST, {
								comment: flagComment,
								article: article.sys_id,
								reason: reason
							});
						}
					}
				},
				stopPropagation: true
			},
			[AA_INSERT_KB_ARTICLE_REQUEST]: {
				...insertKBFeedbackEffect,
				stopPropagation: true
			},
			[AA_INSERT_KB_ARTICLE_SUCCEEDED]: insertKBFeedbackSucceededEffect,
			[AA_INSERT_KB_ARTICLE_FAILED]: insertKBFeedbackFailedEffect
		}
	}
};

export default flagUtils;
