import {
	AA_CONTAINER_SHOW_PREV,
	AA_CONTAINER_SHOW_NEXT
} from '../../sn-aa-details-container/actions';
import { AA_UPDATE_REL_DOC } from '../../sn-aa-action-modal';
import { AA_PAGE_CHANGE_SEARCH } from './searchActionHandlers';
// import { baseLinkFromRootDir } from '../../util/agentAssistUtils';

const showPrevItemEffect = ({ state, action, dispatch, updateState }) => {
	const { selectedIndex, offset } = action.payload;
	const newIndex = selectedIndex - 1;
	const virtualIndex = newIndex - offset;
	if (virtualIndex >= 0) {
		const resultItems = state.searchState.searchResults;
		const resultItem = (virtualIndex < 0 || virtualIndex >= resultItems.length) ? null : resultItems[virtualIndex];
		// const linkToOpen = baseLinkFromRootDir(resultItem.link);
		// if (resultItem && resultItem.linkTarget === '_blank' && linkToOpen) {
		//     window.open(linkToOpen, '_blank');
		// } else {
		updateState({
			detailViewState: {
				showDetail: true,
				selectedItem: resultItem,
				selectedIndex: newIndex
			}
		});
		// }
		dispatch(AA_UPDATE_REL_DOC, { itemData: resultItem, itemIndex: newIndex, actionValue: 'Preview', remove: false });
	} else if (virtualIndex < 0 && offset > 0) {    //related to pagination... #TODO#
		updateState({
			detailViewState: {
				showDetail: true,
				prevPageLoading: true
			}
		});
		dispatch(AA_PAGE_CHANGE_SEARCH, {direction: -1});
	}
};

const showNextItemEffect = ({ state, action, dispatch, updateState }) => {
	const { selectedIndex, offset } = action.payload;
	const newIndex = selectedIndex + 1;
	const virtualIndex = newIndex - offset;
	const resultCount = state.searchState.returnedResults;
	const hasMoreResults = state.searchState.hasMoreResults;
	if (virtualIndex < resultCount) {
		const resultItems = state.searchState.searchResults;
		const resultItem = (virtualIndex < 0 || virtualIndex >= resultItems.length) ? null : resultItems[virtualIndex];
		// const linkToOpen = baseLinkFromRootDir(resultItem.link);
		// if (resultItem && resultItem.linkTarget === '_blank' && linkToOpen) {
		//     window.open(linkToOpen, '_blank');
		// } else {
		updateState({
			detailViewState: {
				showDetail: true,
				selectedItem: resultItem,
				selectedIndex: newIndex
			}
		});
		// }
		dispatch(AA_UPDATE_REL_DOC, { itemData: resultItem, itemIndex: newIndex, actionValue: 'Preview', remove: false });
	} else if (virtualIndex >= resultCount && hasMoreResults) {
		updateState({
			detailViewState: {
				showDetail: true,
				nextPageLoading: true
			}
		});
		dispatch(AA_PAGE_CHANGE_SEARCH, {direction: 1});
	}
};

export const detailsContianerHandlers = {
	[AA_CONTAINER_SHOW_PREV]: {
		effect: showPrevItemEffect,
		stopPropagation: true
	},
	[AA_CONTAINER_SHOW_NEXT]: {
		effect: showNextItemEffect,
		stopPropagation: true
	}
};
