import {
	AA_GET_LATEST_WORKNOTE,
	AA_GET_LATEST_WORKNOTE_SUCCESS,
	AA_GET_LATEST_WORKNOTE_ERROR,
	AA_RECORD_SET_STATE
} from './actions';
import { createHttpEffect } from '@servicenow/ui-effect-http';

const makeGetLatestWorknoteHandler = (successAction, errorAction) => {
	return createHttpEffect(
		'api/now/cxs/get_latest_worknote/:table_name/:record_id',
		{
			method: 'GET',
			pathParams: ['table_name', 'record_id'],
			successActionType: successAction,
			errorActionType: errorAction
		}
	);
};

const getLatestWorknoteSucceed = coeffects => {
	const { state, action, updateState } = coeffects;
	updateState({
		...state,
		workNote: action.payload.result.worknote
	});
};

const getLatestWorknoteError = coeffects => {
	const { state, updateState } = coeffects;
	updateState({
		...state
	});
};

const setRecordDetailState = coeffects => {
	const { state, action, updateState } = coeffects;
	updateState({
		...state,
		showLatestWorkNote: action.payload.showLatestWorkNote
	});
};

export const actionHandlers = {
	[AA_GET_LATEST_WORKNOTE]: {
		...makeGetLatestWorknoteHandler(
			AA_GET_LATEST_WORKNOTE_SUCCESS,
			AA_GET_LATEST_WORKNOTE_ERROR
		),
		stopPropagation: true
	},
	[AA_GET_LATEST_WORKNOTE_SUCCESS]: {
		effect: getLatestWorknoteSucceed,
		stopPropagation: true
	},
	[AA_GET_LATEST_WORKNOTE_ERROR]: {
		effect: getLatestWorknoteError,
		stopPropagation: true
	},
	[AA_RECORD_SET_STATE]: {
		effect: setRecordDetailState,
		stopPropagation: true
	}
};
