import { t } from 'sn-translate';
import '@servicenow/now-toggle';
import { AA_SEARCH_BOX_CONTENT_CHANGED } from './actions';

const handleValueChange = (e, dispatch, formSearchTerm) => {
	if (!e.target.value)
		// TODO: Ideally we don't need null check here but a seismic issue
		// makes formSearchTerm 'undefined' when it's passed down as ''.
		e.target.placeholder = formSearchTerm ? formSearchTerm : '';
	dispatch(AA_SEARCH_BOX_CONTENT_CHANGED, { searchBoxValue: e.target.value, sourceField: 'additional_search' });
};

const view = (state, dispatch) => {
	const {
		properties: { formSearchTerm, searchBoxText, isLoading },
	} = state;
	return (
		<div className="aa-control">
			<div className="aa-control-field">
				<div className="aa-control-icon">
					<now-icon icon="magnifying-glass-fill" size="md" />
				</div>
				<input name="search-box"
					className="aa-input"
					type="text"
					disabled={isLoading}
					aria-label={t('Search')}
					value={searchBoxText}
					placeholder={formSearchTerm}
					maxlength="185"
					on-keyup={e => {
						if (e.key === 'Enter' || !e.target.value) {
							// this.search(e.target.value, 'additional_search');
							// e.target.placeholder = formSearchTerm;
							handleValueChange(e, dispatch, formSearchTerm);
						}
					}}
					on-blur={e => { handleValueChange(e, dispatch, formSearchTerm); }}
					on-focus={e => e.target.placeholder = ''}
				/>
			</div>
		</div>
	);
};

export default view;
